import * as React from 'react';

import { Divider } from 'antd';

import isBoolean from 'lodash/isBoolean';
import isString from 'lodash/isString';

import { EDividerViewType, EPropRsType } from '_app/Enums';
import { IErrorBase, WidgetClassStateless } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getWidgetPropertyError, propIsStringOrNumber } from '_components/widget/widgetUtils';

import { IProps } from './interfaces';

/**
 * Блок разделителя.
 */
export class DividerBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Разделитель.';

  static validator(props: IProps): IErrorBase[] {
    const { text, viewType, plain } = props;
    let errors: IErrorBase[] = [];

    /**
     * type должен быть строкой.
     */
    if (!isString(viewType)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'viewType', [EPropRsType.STRING], viewType),
      );
    }

    /**
     * text должен быть строкой или числом.
     */
    if (canBeEmptyProp(text) && propIsStringOrNumber(text)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'text',
          [EPropRsType.STRING, EPropRsType.NUMBER],
          text,
        ),
      );
    }

    /**
     * plain должен быть boolean.
     */
    if (canBeEmptyProp(plain) && !isBoolean(plain)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'plain', [EPropRsType.BOOLEAN], plain),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.text !== nextProps.text ||
      this.props.viewType !== nextProps.viewType ||
      this.props.plain !== nextProps.plain
    );
  }

  render() {
    return (
      <Divider
        plain={this.props.plain ?? false}
        dashed={this.props.viewType === EDividerViewType.DASHED}
      >
        {this.props.text}
      </Divider>
    );
  }
}
