import * as React from 'react';

import { Form, Switch } from 'antd';

import isBoolean from 'lodash/isBoolean';
import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { getWidgetContextError, getWidgetPropertyError } from './widgetUtils';

/**
 * Свойства тумблера.
 *
 * @prop {string} [label] Подпись для поля ввода.
 * @prop {number} [disabled] Возможно ли редактирование.
 * @prop {Function} [onFocus] Функция вызываемая при фокусе компонента.
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps extends IWidgetBaseProps<boolean> {
  label?: string;
  disabled?: boolean;
  onFocus?: () => void;
  readOnly?: boolean;
  layout?: EFormLayout;
}

/**
 * Обёртка над Switch из ufs-ui.
 */
export class SwitchBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Тумблер';

  static validator(props: IProps): IErrorBase[] {
    const { value, label, disabled, layout } = props;
    let errors: IErrorBase[] = [];

    /**
     * value должен быть логическим.
     */
    if (value !== null && !isBoolean(value)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.BOOLEAN], value));
    }

    /**
     * label должен быть строкой.
     */
    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    /**
     * disabled должен быть логическим.
     */
    if (canBeEmptyProp(disabled) && !isBoolean(disabled)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'disabled',
          [EPropRsType.BOOLEAN],
          disabled,
        ),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  componentDidMount() {
    if (this.props.value === null) {
      this.props.onChange(false);
    }
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.value !== nextProps.value ||
      this.props.label !== nextProps.label ||
      this.props.disabled !== nextProps.disabled ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.layout !== nextProps.layout
    );
  }

  render() {
    const { value, layout } = this.props;

    return (
      <FormWidgetContainer layout={layout}>
        <Form.Item
          data-test-id="SwitchBlock.Component"
          required={this.props.onlyShow ? false : this.props.isRequired}
          label={this.props.label ?? ' '}
          colon={false}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <Switch
            data-test-id="SwitchBlock.switch"
            checked={value}
            onChange={this.props.onChange}
            disabled={this.props.onlyShow || this.props.disabled || this.props.readOnly}
          />
        </Form.Item>
      </FormWidgetContainer>
    );
  }
}
