import * as React from 'react';

import { Form, DatePicker, TimePicker } from 'antd';
import dayjs from 'dayjs';
import moment, { Moment } from 'moment';

import locale from 'antd/lib/date-picker/locale/ru_RU';

import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import {
  getEnumError,
  getEnumKeys,
  getWidgetContextError,
  getWidgetPropertyError,
} from './widgetUtils';

/**
 * Свойства поля выбора даты.
 *
 * @prop {string} label Подпись над полем ввода.
 * @prop {string} [dateType] Тип поля.
 * @prop {Date} [min] Минимальное значение.
 * @prop {Date} [max] Максимальное значение.
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {string} format Формат даты.
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps extends IWidgetBaseProps<Date> {
  label?: string;
  dateType?: string;
  min?: Date;
  max?: Date;
  readOnly?: boolean;
  format?: string;
  layout?: EFormLayout;
}

/**
 * Типы полей даты.
 *
 * DATE_PICKER - Выбор дня в году (по умолчанию).
 * WEEK_PICKER - Выбор недели.
 * MONTH_INPUT - Выбор месяца.
 * QUARTER_PICKER - Выбор квартала.
 * YEAR_PICKER - Выбор года.
 * DATE_TIME_PICKER - Выбор дня и времени.
 * TIME_PICKER - Выбор времени.
 */
export enum EDateInputTypes {
  DATE_PICKER = 'DATE_PICKER',
  WEEK_PICKER = 'WEEK_PICKER',
  MONTH_INPUT = 'MONTH_INPUT',
  QUARTER_PICKER = 'QUARTER_PICKER',
  YEAR_PICKER = 'YEAR_PICKER',
  DATE_TIME_PICKER = 'DATE_TIME_PICKER',
  TIME_PICKER = 'TIME_PICKER',
}

/**
 * Поле выбора даты.
 */
export class DateInputBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Выбор даты';

  static validator(props: IProps): IErrorBase[] {
    const { value, label, dateType, format, layout } = props;

    let errors: IErrorBase[] = [];

    function validatePropDate(nameProp: keyof IProps, prop: IProps, widgetName: string) {
      const valueProp = prop[nameProp] as string;
      if (canBeEmptyProp(valueProp) && !dayjs(valueProp).isValid()) {
        errors.push(
          getWidgetPropertyError<IProps>(widgetName, nameProp, [EPropRsType.DATE], valueProp),
        );
      }
    }

    /**
     * Значение должно быть датой.
     */
    if (value !== null && !dayjs(value).isValid()) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.DATE], value));
    }

    /**
     * min должно быть датой.
     */
    validatePropDate('min', props, this.widgetName);

    /**
     * max должно быть датой.
     */
    validatePropDate('max', props, this.widgetName);

    /**
     * label должен быть строкой.
     */
    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    const availableDateTypes = getEnumKeys(EDateInputTypes);

    /**
     * format должен быть строкой.
     */
    if (canBeEmptyProp(format) && !isString(format)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'format', [EPropRsType.STRING], format),
      );
    }

    /**
     * dateType должен быть строкой.
     */
    if (!isString(dateType)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'dateType', [EPropRsType.STRING], dateType),
      );
    } else if (availableDateTypes.indexOf(dateType) < 0) {
      errors.push(getEnumError<IProps>(availableDateTypes, dateType, this.widgetName, 'dateType'));
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.value !== nextProps.value ||
      this.props.label !== nextProps.label ||
      this.props.min !== nextProps.min ||
      this.props.max !== nextProps.max ||
      this.props.dateType !== nextProps.dateType ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.format !== nextProps.format ||
      this.props.layout !== nextProps.layout
    );
  }

  /**
   * Трансформация типа даты из строки в Enum предназначенный для DateInput.
   */
  transformType = (dateType: string): 'time' | 'date' | 'week' | 'month' | 'quarter' | 'year' => {
    switch (dateType) {
      case EDateInputTypes.DATE_PICKER:
        return 'date';
      case EDateInputTypes.WEEK_PICKER:
        return 'week';
      case EDateInputTypes.MONTH_INPUT:
        return 'month';
      case EDateInputTypes.QUARTER_PICKER:
        return 'quarter';
      case EDateInputTypes.YEAR_PICKER:
        return 'year';
      default:
        return 'date';
    }
  };

  getDisabledDate = (d: Moment): boolean => {
    const { min, max } = this.props;
    return !d || (max ? d.isAfter(max) : false) || (min ? d.isSameOrBefore(min) : false);
  };

  render() {
    const { dateType, value, format } = this.props;

    return (
      <FormWidgetContainer layout={this.props.layout}>
        <Form.Item
          data-test-id="DateInputBlock.Component"
          required={this.props.onlyShow === true ? false : this.props.isRequired}
          label={this.props.label ?? ' '}
          colon={false}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          {dateType && dateType === EDateInputTypes.TIME_PICKER ? (
            <TimePicker
              data-test-id="DateInputBlock.time"
              value={value ? moment(value) : null}
              disabledDate={this.getDisabledDate}
              onChange={date => this.props.onChange(date ? date.toDate() : null)}
              disabled={this.props.onlyShow || this.props.readOnly}
              onBlur={this.props.onBlur}
              format={format}
              locale={locale}
            />
          ) : (
            <DatePicker
              data-test-id="DateInputBlock.date"
              value={value ? moment(value) : null}
              picker={this.transformType(dateType)}
              disabledDate={this.getDisabledDate}
              onChange={date => this.props.onChange(date ? date.toDate() : null)}
              disabled={this.props.onlyShow || this.props.readOnly}
              onBlur={this.props.onBlur}
              format={format}
              locale={locale}
              showTime={dateType === EDateInputTypes.DATE_TIME_PICKER}
            />
          )}
        </Form.Item>
      </FormWidgetContainer>
    );
  }
}
