import { EBlockType, EPropRsType } from './Enums';
import {
  IMacroDefinitionRs,
  IPropsRs,
  IScriptDetails,
  IScriptRs,
  IExpressionWithOriginal,
  IPlayerContext,
  TESection,
  TLowercase,
  TEPropRsType,
  IBlockRs,
} from './Models';
import { ContextPath } from './WidgetPropTypes/ContextPath';
import { TemplatedString } from './WidgetPropTypes/TemplatedString';

/**
 * Преобразование скрипта из ответа на сервере.
 *
 * @param {IScriptRs} scriptResponse Ответ REST-сервиса.
 */
export function convertScriptFromServer(scriptResponse: IScriptRs): IScriptDetails {
  return {
    ...scriptResponse,
    // Преобразование макросов
    macros: convertMacroFromServer(scriptResponse.macros),
    // Преобразование свойств виджетов блоков-экранов
    blocks: scriptResponse.blocks.map((block: IBlockRs) => {
      if (block.type === EBlockType.SCREEN) {
        // Конвертируем выражения для отображения строк в блоке экрана.
        const visibility = block.visibility
          ? block.visibility.reduce((previousValue, currentValue) => {
              previousValue[currentValue.rowNumber] = currentValue.expression;
              return previousValue;
            }, {})
          : null;

        // Конвертируем свойства виджетов блока экрана.
        const widgets = block.widgets.map((widget, index) => {
          return {
            index,
            ...widget,
            props: propsFromServerFormat(widget.props),
          };
        });

        return {
          ...block,
          visibility,
          widgets,
        };
      } else {
        return block;
      }
    }),
  };
}

/**
 * Преобразование свойства виджетов, полученных с сервера, в используемый проигрывателем формат.
 */
export function propsFromServerFormat(props: IPropsRs[]) {
  let result = {};
  props &&
    props.forEach(prop => {
      result = {
        ...result,
        ...convertPropFromServer(prop),
      };
    });
  return result;
}

/**
 * Преобразование свойства виджетов, полученных с сервера, в используемый проигрывателем формат.
 *
 * @param {IPropsRs} serverProp Свойство в формате с сервера.
 */
function convertPropFromServer(serverProp: IPropsRs): any {
  const { name, value, type, props } = serverProp;
  let result;
  switch (type) {
    case EPropRsType.STRING:
      result = value;
      break;
    case EPropRsType.NUMBER:
      result = Number(value);
      break;
    case EPropRsType.BOOLEAN:
      result = value === 'true' || value === true;
      break;
    case EPropRsType.OBJECT:
      let temp = {};
      props.forEach(prop => {
        temp = {
          ...temp,
          ...convertPropFromServer(prop),
        };
      });
      result = temp;
      break;
    case EPropRsType.ARRAY:
      result = props.map(convertPropFromServer);
      break;
    case EPropRsType.DATE:
      result = new Date(value as string);
      break;
    case EPropRsType.TEMPLATE:
      result = new TemplatedString(value as string);
      break;
    case EPropRsType.LINK:
      result = new ContextPath(value as string);
      break;
    case EPropRsType.EXPRESSION:
      result = value as IExpressionWithOriginal;
      break;

    default:
      throw new Error(`Для компонента с сервера получен неизвестный тип свойств: ${type}!`);
  }
  return name ? { [name]: result } : result;
}

/**
 * Преобразование свойства виджетов, полученных с сервера, в используемый проигрывателем формат.
 *
 * @param {string} value значение с строковом формате
 * @param {IPropsRs} type Свойство в формате с сервера.
 */
export function convertSimpleValue(value: string, type: TEPropRsType): unknown {
  switch (type) {
    case EPropRsType.STRING:
      return value;
    case EPropRsType.NUMBER:
      return Number(value);
    case EPropRsType.BOOLEAN:
      return value === 'true';

    default:
      throw new Error(`Получен неизвестный тип свойств: ${type}!`);
  }
}

/**
 * Преобразование макросов, полученных с сервера, в используемый проигрывателем формат.
 *
 * @param {IMacroDefinitionRs[]} macros макросы
 */
export function convertMacroFromServer(macros: IMacroDefinitionRs[]) {
  const macrosMap = {};
  macros &&
    macros.forEach(macro => {
      macrosMap[macro.functionName] = macro.expression;
    });
  return macrosMap;
}

/**
 * Преобразование данных секции.
 *
 * @param {string | boolean} value значение
 * @param {EPropRsType | TESection} type тип данных
 * @param {IPlayerContext} context текущий контекст
 */
export function getSectionParam(
  value: string | boolean,
  type: TLowercase<EPropRsType.BOOLEAN> | TESection,
  context: IPlayerContext,
) {
  switch (type) {
    case EPropRsType.STRING:
    case EPropRsType.BOOLEAN:
      return value;
    case EPropRsType.LINK:
      return new ContextPath(value as string).getValue(context);
    case EPropRsType.TEMPLATE:
      return new TemplatedString(value as string).getValue(context);
    default:
      throw new Error(`Получен неизвестный тип свойств: ${type}!`);
  }
}
