import { IScriptRs } from '../Models';

export class ScriptInformation {
  // Идентификация скрипта.
  public subject: string;

  setSubject(script: IScriptRs) {
    this.subject = script.subject;
  }

  getSubject(): string {
    return this.subject;
  }
}

// Специальная константа для хранения идентификатора скрипта при логировании ошибок.
export const scriptInformation: ScriptInformation = new ScriptInformation();
