abstract class ISubSysCode {
  public subsystemCode: string;
  public abstract getSubsystemCode();
  public abstract setSubsystemCode(subsystemCode: string);
}

// Специальная константа для хранения subSystemCode для логирования ошибок.
export const SUB_SYSTEM_CODE: any = new (class SubSysCode extends ISubSysCode {
  public subsystemCode: string = '';
  getSubsystemCode(): string {
    return this.subsystemCode;
  }
  setSubsystemCode(subsystemCode: string) {
    this.subsystemCode = subsystemCode;
  }
})();

