import * as React from 'react';

import { Form, Typography } from 'antd';

import isBoolean from 'lodash/isBoolean';
import isString from 'lodash/isString';

import { EPropRsType } from '_app/Enums';
import { IErrorBase, WidgetClassStateless } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';
import { getWidgetPropertyError, propIsStringOrNumber } from '_components/widget/widgetUtils';

import { IProps } from './interfaces';

/**
 * Текстовое поле с заголовком.
 */
export class TextFieldWithTitle extends WidgetClassStateless<IProps> {
  static widgetName = 'Текстовое поле с заголовком.';

  static validator(props: IProps): IErrorBase[] {
    const { text, label, hint, copyable, layout } = props;
    let errors: IErrorBase[] = [];

    /**
     * text должен быть строкой или числом.
     */
    if (propIsStringOrNumber(text)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'text',
          [EPropRsType.STRING, EPropRsType.NUMBER],
          text,
        ),
      );
    }

    /**
     * label должен быть строкой.
     */
    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    /**
     * hint должен быть строкой.
     */
    if (canBeEmptyProp(hint) && !isString(hint)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'hint', [EPropRsType.STRING], hint),
      );
    }

    /**
     * copyable должен быть boolean.
     */
    if (canBeEmptyProp(copyable) && !isBoolean(copyable)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'copyable',
          [EPropRsType.BOOLEAN],
          copyable,
        ),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.text !== nextProps.text ||
      this.props.label !== nextProps.label ||
      this.props.hint !== nextProps.hint ||
      this.props.copyable !== nextProps.copyable ||
      this.props.layout !== nextProps.layout
    );
  }

  render() {
    return (
      <FormWidgetContainer layout={this.props.layout}>
        <Form.Item
          label={this.props.label ?? ' '}
          colon={false}
          tooltip={getFormItemHint(this.props.hint)}
          data-test-id="TextFieldWithTitle.Component"
          className="ssp-player-text-field-with-title"
        >
          <Typography.Text
            className="ssp-player-text-field-with-title__text"
            copyable={this.props.copyable}
          >
            {this.props.text}
          </Typography.Text>
        </Form.Item>
      </FormWidgetContainer>
    );
  }
}
