/**
 * Типы выбора диапазона дат.
 *
 * DAY - Выбор дня (по умолчанию).
 * DAY_TIME - Выбор дня и временем.
 * MONTH - Выбор месяца.
 * WEEK - Выбор квартала.
 * YEAR - Выбор года.
 */
export enum EDateRangeInputTypes {
  DAY = 'DAY',
  DAY_TIME = 'DAY_TIME',
  MONTH = 'MONTH',
  WEEK = 'WEEK',
  YEAR = 'YEAR',
}
