import React from 'react';
import { useDispatch } from 'react-redux';

import { PlayerActions } from '_actions/PlayerActions';
import { ICustomWidgetBaseProps } from '_app/Models';
import { prepareCalculateErrorExpected } from '_app/errors';
import { useRemoteComponent } from '_utils/hooks';

export const CustomWidget = ({ additionProps, ...props }: ICustomWidgetBaseProps<string>) => {
  const { url, componentName } = additionProps;
  const { scriptName } = props;
  const [loading, err, Component] = useRemoteComponent(url);
  const dispatch = useDispatch();

  if (loading) {
    return <div>Загрузка виджета...</div>;
  }

  if (err) {
    dispatch(
      PlayerActions.playerErrorAction(
        scriptName,
        prepareCalculateErrorExpected({
          title: `Ошибка при загрузке кастомного виджета ${componentName}`,
          message: err.message,
          stack: err.stack,
        }),
      ),
    );

    return null;
  }

  return <Component name={'custom'} {...props} />;
};
