import { AlignType } from 'rc-table/lib/interface';
import * as React from 'react';

import { Table, Typography, Statistic } from 'antd';

import isArray from 'lodash/isArray';
import isNumber from 'lodash/isNumber';
import isString from 'lodash/isString';
import isDate from 'lodash/isDate';
import isBoolean from 'lodash/isBoolean';

import { EHorizontalAlign, EVerticalAlign, EColumnStyle, EPropRsType } from '_app/Enums';
import { IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '_app/Models';
import { canBeEmptyProp, checkIsBigDecimal } from '_app/utils';

import {
  AVAILABLE_HORIZONTAL_ALIGNS,
  AVAILABLE_VERTICAL_ALIGNS,
  AVAILABLE_COLUMN_STYLES,
} from '../Const';
import { getWidgetOptionError, getWidgetPropertyError, getEnumErrorInOptions } from './widgetUtils';

const { Text } = Typography;

/**
 * Параметры колонок.
 *
 * @prop {string} [label] Заголовок колонки.
 * @prop {string} [width] Ширина колонок с префиксом "px" или "%".
 * @prop {EHorizontalAlign} [hAlign] Выравнивание по горизонтали.
 * @prop {EVerticalAlign} [vAlign] Выравнивание по вертикали.
 * @prop {EColumnStyle} [columnStyle] Стиль оформления колонки.
 */
export interface ITableHeader {
  label?: string;
  width?: string;
  hAlign?: EHorizontalAlign;
  vAlign?: EVerticalAlign;
  columnStyle?: EColumnStyle;
}

/**
 * Свойства табличного блока.
 *
 * @prop {string} [caption] Заголовок таблицы.
 * @prop {ITableHeader[]} headers Параметры колонок.
 * @prop {any[][]} rows Данные для отображения в таблице.
 */
export interface IProps extends IWidgetBaseProps<void> {
  caption?: string;
  headers: ITableHeader[];
  rows: any[][];
}

/**
 * Табличный блок.
 */
export class PlainTableBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Таблица';

  static validator(props: IProps): IErrorBase[] {
    const { caption, rows, headers } = props;
    let errors: IErrorBase[] = [];

    /**
     * caption должен быть строкой.
     */
    if (canBeEmptyProp(caption) && !isString(caption)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'caption', [EPropRsType.STRING], caption),
      );
    }

    /**
     * Значение rows должно быть массивом объектов, состоящим из массива строк.
     */
    if (isArray(rows)) {
      // Что бы не вываливать весь список ошибок, если передан массив с другими объектами, показываем только первую встречную ошибку.
      rows.some((row: any[], rowIndex: number) => {
        const getError = getWidgetOptionError<IProps, any[]>(this.widgetName, 'rows');

        let hasError = false;
        if (isArray(row)) {
          row.some((cellValue: any, indexCell: number) => {
            const getErrorRow = getWidgetOptionError<IProps, any[]>(this.widgetName, 'rows');
            if (
              canBeEmptyProp(cellValue) &&
              !(isString(cellValue) || isNumber(cellValue) || checkIsBigDecimal(cellValue) || isDate(cellValue) || isBoolean(cellValue))
            ) {
              errors.push(
                getErrorRow(null, [EPropRsType.STRING, EPropRsType.NUMBER, EPropRsType.DATE, EPropRsType.BOOLEAN], cellValue, indexCell),
              );
            }
            return hasError;
          });
        } else {
          hasError = true;
          errors.push(getError(null, [EPropRsType.ARRAY], row, rowIndex));
        }

        return hasError;
      });
    } else {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'rows', [EPropRsType.ARRAY], rows),
      );
    }

    /**
     * Значение headers должно быть массивом объектов.
     */
    if (isArray(headers)) {
      // Что бы не вываливать весь список ошибок, если передан массив с другими объектами, показываем только первую встречную ошибку.
      headers.some((tableHeader: ITableHeader, headerIndex: number) => {
        const getError = getWidgetOptionError<IProps, ITableHeader>(this.widgetName, 'headers');
        const getErrorInOption = getEnumErrorInOptions<IProps, ITableHeader>(
          this.widgetName,
          'headers',
        );
        let hasError = false;

        function validateProp(
          nameProp: keyof ITableHeader,
          prop: ITableHeader,
          availableValues: EPropRsType[],
        ) {
          const valueForCheck = prop[nameProp];
          if (canBeEmptyProp(valueForCheck) && !isString(valueForCheck)) {
            hasError = true;
            errors.push(getError(nameProp, availableValues, valueForCheck, headerIndex));
          }
        }

        function validatePropEnum(
          nameProp: keyof ITableHeader,
          prop: ITableHeader,
          availableValues: string[],
        ) {
          const valueForCheck = prop[nameProp];
          if (canBeEmptyProp(valueForCheck) && !isString(valueForCheck)) {
            hasError = true;
            errors.push(getError(nameProp, [EPropRsType.STRING], valueForCheck, headerIndex));
          } else if (
            canBeEmptyProp(valueForCheck) &&
            availableValues.indexOf(`${valueForCheck}`) < 0
          ) {
            hasError = true;
            errors.push(
              getErrorInOption(availableValues, `${valueForCheck}`, nameProp, headerIndex),
            );
          }
        }

        validateProp('label', tableHeader, [EPropRsType.STRING]);
        validatePropEnum('columnStyle', tableHeader, AVAILABLE_COLUMN_STYLES);
        validatePropEnum('hAlign', tableHeader, AVAILABLE_HORIZONTAL_ALIGNS);
        validatePropEnum('vAlign', tableHeader, AVAILABLE_VERTICAL_ALIGNS);
        validateProp('width', tableHeader, [EPropRsType.STRING]);

        return hasError;
      });
    } else {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'headers', [EPropRsType.ARRAY], headers),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.caption !== nextProps.caption ||
      this.props.headers !== nextProps.headers ||
      this.props.rows !== nextProps.rows
    );
  }

  render() {
    const { caption, headers, rows } = this.props;

    const dataSource = rows.map((row, i) => {
      return row.reduce(
        (ds, v, i) => {
          let val = v;
          /** Преобразование даты в строку */
          if (isDate(v)) {
            val = v.toLocaleDateString();
          }
          /** Преобразование булевского значения в строку */
          if (isBoolean(v)) {
            val = v.toString();
          }

          return {
            ...ds,
            [`field${i}`]: val,
          };
        },
        { key: i },
      );
    });

    const renderCell = (columnStyle: EColumnStyle, text: string) => {
      switch (columnStyle) {
        case EColumnStyle.BOLD:
          return <Text strong>{text}</Text>;
        case EColumnStyle.MONEY:
          return <Statistic value={text} precision={2} />;
        case EColumnStyle.NUMBER:
          return <Statistic value={text} precision={0} />;
        case EColumnStyle.DEFAULT:
        default:
          return text;
      }
    };

    const columns = headers.map((header, i) => {
      return {
        title: <span data-test-id={`PlainTableBlock.column.${i}`}>{header.label}</span>,
        dataIndex: `field${i}`,
        key: `field${i}`,
        align: `${header.hAlign}` as AlignType,
        width: header.width,
        render: (text: string) => {
          return (
            <span data-test-id={`PlainTableBlock.cell.${i}`}>
              {renderCell(header.columnStyle, text)}
            </span>
          );
        },
        sorter: (a: any, b: any) => {
          const left = a[`field${i}`];
          const right = b[`field${i}`];
          if (isString(left) && isString(right)) {
            return left.localeCompare(right);
          } else {
            return (left as number) - (right as number);
          }
        },
      };
    });

    return (
      <Table
        data-test-id="PlainTableBlock.Component"
        title={caption ? () => { return <strong data-test-id="PlainTableBlock.Title">{caption}</strong> } : undefined }
        columns={columns}
        dataSource={dataSource}
        pagination={false}
      />
    );
  }
}
