import get from 'lodash/get';

import { IPlayerContext } from '../Models';
import { checkIsBigDecimal } from '../utils';

/**
 * Класс для значений переменных.
 * Используется для реализации типа "link" при задании свойств виджета экрана.
 */
export class ContextPath {
  // специальный флаг
  isContextPath: boolean = true;

  // Путь до переменной в контексте
  contextPath: string = null;

  constructor(contextPath: string) {
    this.contextPath = contextPath;
  }

  /**
   * Контекст для поиска переменной по пути contextName.
   *
   * @param {IPlayerContext} context Контекст с переменными.
   */
  getValue(context: IPlayerContext) {
    let contextValue: any = get(context, this.contextPath, new Error());
    if (contextValue instanceof Error) {
      throw new Error(
        `Не удалось подставить переменные из контекста в переменную из: "${this.contextPath}"`,
      );
    } else if (checkIsBigDecimal(contextValue)) {
      // BPSBSCP-599: Big.js объект приводит к различным неожиданным ошибкам в виджетах
      // поэтому при его чтениии из контекста его необходимо преобразовывать в простое число
      contextValue = +contextValue; // аналог Number(contextValue)
    }
    return contextValue;
  }
}
