import * as React from 'react';

import { EPropRsType } from '_app/Enums';

import { IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '../../Models';
import { getWidgetPropertyError, propIsStringOrNumber } from './widgetUtils';

/**
 * Свойства текстового блока.
 *
 * @prop {string | number} text Текст, выводимый пользователю.
 */
export interface IProps extends IWidgetBaseProps<void> {
  text: string | number;
}

/**
 * Текстовый блок.
 */
export class TextBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Отображение текста';

  static validator(props: IProps): IErrorBase[] {
    const { text } = props;
    let errors: IErrorBase[] = [];

    /**
     * text должен быть строкой или числом.
     */
    if (propIsStringOrNumber(text)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'text',
          [EPropRsType.STRING, EPropRsType.NUMBER],
          text,
        ),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return this.props.text !== nextProps.text;
  }

  render() {
    return (
      <div
        data-test-id="TextBlock.Component"
        className="ssp-player-text-block"
      >{`${this.props.text}`}</div>
    );
  }
}
