import { LinkOutlined } from '@ant-design/icons';
import React from 'react';

import { Alert, Space, Typography } from 'antd';

import isArray from 'lodash/isArray';
import isString from 'lodash/isString';

import { EPropRsType } from '_app/Enums';
import { IErrorBase, WidgetClassStateless } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { MarkdownReader } from '_components/MardownReader';
import {
  getWidgetContextError,
  getWidgetPropertyError,
  validateDefaultOptions,
} from '_components/widget/widgetUtils';

import { IProps } from './interfaces';
import { ELinkDirection, ETargetType } from './сonstants';

/** Компонент Алерт */
export class AlertBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Алерт';

  static validator(props: IProps): IErrorBase[] {
    const { links, label, text, linkDirection, alertType } = props;

    let errors: IErrorBase[] = [];

    if (links) {
      if (isArray(links)) {
        errors.push(...validateDefaultOptions(links, this.widgetName));
      } else {
        errors.push(
          getWidgetPropertyError<IProps>(this.widgetName, 'links', [EPropRsType.ARRAY], links),
        );
      }
    }

    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.STRING], label));
    }

    if (canBeEmptyProp(text) && !isString(text)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.STRING], text));
    }

    if (linkDirection !== null && !isString(linkDirection)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.STRING], linkDirection));
    }

    if (alertType !== null && !isString(alertType)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.STRING], alertType));
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.links !== nextProps.links ||
      this.props.label !== nextProps.label ||
      this.props.text !== nextProps.text ||
      this.props.linkDirection !== nextProps.linkDirection ||
      this.props.alertType !== nextProps.alertType
    );
  }

  render() {
    const { links, label, text, linkDirection, alertType } = this.props;

    const isVerticalDirection = linkDirection === ELinkDirection.VERTICAL;

    return (
      <Alert
        message={label}
        description={
          <>
            {text && <MarkdownReader content={text} />}
            {links && links.length > 0 && (
              <Space
                className="ssp-player-alert-links"
                direction={isVerticalDirection ? 'vertical' : 'horizontal'}
              >
                {links.map(link => {
                  const isTargetBlank = link.select === ETargetType.BLANK;

                  return (
                    <Typography.Link href={link.value} key={link.value} target={link.select}>
                      <span className="ssp-player-alert-links__label">{link.label}</span>
                      {isTargetBlank && <LinkOutlined />}
                    </Typography.Link>
                  );
                })}
              </Space>
            )}
          </>
        }
        type={alertType}
        showIcon
        className="ssp-player-alert"
      />
    );
  }
}
