import { IToken } from '@sber-ssp-ui/spruce';
import * as React from 'react';
import Highlighter from 'react-highlight-words';

import { Button, Empty, Form, Input, Space, Typography } from 'antd';

import get from 'lodash/get';
import isArray from 'lodash/isArray';
import isBoolean from 'lodash/isBoolean';
import isEqual from 'lodash/isEqual';
import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IErrorBase, IScreenMenuItem, IScreenMenuProps, WidgetClass } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { getWidgetOptionError, getWidgetPropertyError } from './widgetUtils';

const { Text } = Typography;

/**
 * Свойства компонента.
 */
export interface IProps extends IScreenMenuProps {}

/**
 * Состояние компонента.
 *
 * @prop {string} [query] Запрос.
 */
export interface IState {
  query?: string;
}

export const NO_RESULT = 'Ничего не найдено';

/**
 * Список с переходами (меню).
 */
export class MenuRoute extends WidgetClass<IProps, IState> {
  static widgetName = 'Список с переходами';

  props: IProps;

  state: IState = {
    query: '',
  };

  static validator(props: IProps): IErrorBase[] {
    const { hasSearch, options, interpreter } = props;
    let errors: IErrorBase[] = [];

    /**
     * hasSearch должен быть логическим типом.
     */
    if (canBeEmptyProp(hasSearch) && !isBoolean(hasSearch)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'hasSearch',
          [EPropRsType.BOOLEAN],
          hasSearch,
        ),
      );
    }

    /**
     * Значение options должно быть массивом объектов.
     */
    if (!isArray(options)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'options', [EPropRsType.ARRAY], options),
      );
    } else if (isArray(options)) {
      // Что бы не вываливать весь список ошибок, если передан массив с другими объектами, показываем только первую встречную ошибку.
      options.some(
        (
          { label, infoVariable, nextId, disabledExpression }: IScreenMenuItem,
          optionIndex: number,
        ) => {
          const getError = getWidgetOptionError<IProps, IScreenMenuItem>(
            this.widgetName,
            'options',
          );

          let hasError = false;
          if (!isString(label)) {
            hasError = true;
            errors.push(getError('label', [EPropRsType.STRING], label, optionIndex));
          }
          if (canBeEmptyProp(infoVariable) && !isString(infoVariable)) {
            hasError = true;
            errors.push(getError('infoVariable', [EPropRsType.STRING], infoVariable, optionIndex));
          }
          if (!isString(nextId)) {
            hasError = true;
            errors.push(getError('nextId', [EPropRsType.STRING], nextId, optionIndex));
          }

          if (canBeEmptyProp(disabledExpression)) {
            const title = `В виджете "${this.widgetName}" произошла ошибка при вычислении выражения недоступности пункта меню.`;
            if (disabledExpression.expression) {
              try {
                !!interpreter.interpret(disabledExpression.expression as IToken);
              } catch (e) {
                hasError = true;
                errors.push({
                  title,
                  text:
                    `В свойстве "disabledExpression" по индексу "${optionIndex}" в массиве "options" при вычислении ` +
                    `выражения произошла ошибка: ${e.message}`,
                });
              }
            } else if (disabledExpression.expression !== null) {
              // Если выражение было не задано, приходит null.
              hasError = true;
              errors.push({
                title,
                text: `В свойстве "disabledExpression" не указан параметр "expression" по индексу ${optionIndex} в массиве "options".`,
              });
            }
          }

          return hasError;
        },
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps, nextState: IState) {
    return (
      this.props.hasSearch !== nextProps.hasSearch ||
      this.props.options !== nextProps.options ||
      this.props.interpreter !== nextProps.interpreter ||
      this.state.query !== nextState.query ||
      !isEqual(this.props.playerContext, nextProps.playerContext)
    );
  }

  handleQueryChange = (query: string): void => this.setState({ query });

  handleClickOption = (option: IScreenMenuItem, disabled: boolean, index: number) => () => {
    const { options, onSwitchToBlock, onValidateClick } = this.props;

    if (disabled) {
      return;
    }

    if (options[index]?.shouldValidateItem) {
      const isValidate = onValidateClick(null, true);

      if (isValidate) {
        onSwitchToBlock(option.nextId);
      }

      return;
    }

    onSwitchToBlock(option.nextId);
  };

  /**
   * Вывод опции на экран.
   */
  renderOption = (option: IScreenMenuItem, additionalInfo: string, index: number) => {
    const { interpreter } = this.props;
    const { disabledExpression, label } = option;
    let disabled = false;
    if (disabledExpression && disabledExpression.expression) {
      disabled = !!interpreter.interpret(disabledExpression.expression as IToken);
    }

    return (
      <Button
        key={label}
        data-test-id={`MenuRoute.option.${index}`}
        block
        disabled={disabled}
        onClick={this.handleClickOption(option, disabled, index)}
      >
        <Space direction="horizontal">
          <Highlighter
            data-test-id="MenuRoute.option.primary"
            searchWords={[this.state.query]}
            caseSensitive={false}
            textToHighlight={label}
          />
          {additionalInfo ? (
            <Text type="secondary">
              <Highlighter
                data-test-id="MenuRoute.option.secondary"
                searchWords={[this.state.query]}
                caseSensitive={false}
                textToHighlight={additionalInfo}
              />
            </Text>
          ) : null}
        </Space>
      </Button>
    );
  };

  render() {
    const { hasSearch, options, playerContext } = this.props;

    const items = options
      .map(menu => {
        return {
          menu,
          additionalInfo: `${get(playerContext, menu.infoVariable, '')}`,
        };
      })
      .filter(
        v =>
          v.menu.label.toLowerCase().indexOf(this.state.query.toLowerCase()) >= 0 ||
          (v.additionalInfo ?? '').toLowerCase().indexOf(this.state.query.toLowerCase()) >= 0,
      );

    return (
      <div data-test-id="MenuRoute.Component">
        {hasSearch && (
          <FormWidgetContainer layout={EFormLayout.VERTICAL}>
            <Form.Item label="Фильтр">
              <Input
                data-test-id="MenuRoute.input.filter"
                value={this.state.query}
                onChange={ev => this.handleQueryChange(ev.target.value)}
              />
            </Form.Item>
          </FormWidgetContainer>
        )}
        {items.length > 0 ? (
          items.map(({ menu, additionalInfo }, index) =>
            this.renderOption(menu, additionalInfo, index),
          )
        ) : (
          <Empty data-test-id="MenuRoute.noItem" description={NO_RESULT} />
        )}
      </div>
    );
  }
}
