import * as React from 'react';

import { Form, Select } from 'antd';

import isArray from 'lodash/isArray';
import isBoolean from 'lodash/isBoolean';
import isEqual from 'lodash/isEqual';
import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IOption, IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { NOT_FOUND_SELECT } from '../Const';
import {
  getWidgetPropertyError,
  getWidgetContextError,
  validateDefaultOptions,
  propIsStringOrNumber,
} from './widgetUtils';

const { Option } = Select;

/**
 * Свойства выпадающего списка.
 *
 * @prop {string} [label] Подпись над полем ввода.
 * @prop {string} [caption] Заголовок выпадающего списка.
 * @prop {boolean} [isSelectedDefault] Выбрано значение по дефолту.
 * @prop {T} options Список опций, может быть так же ContextPath, в reducers из-за этого проблемы,
 *                   так как там подставляется значение по умолчанию.
 *                   FIXME: ContextPath используется для типизации reducer
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps<T> extends IWidgetBaseProps<string> {
  label?: string;
  caption?: string;
  isSelectedDefault?: boolean;
  options: T;
  readOnly?: boolean;
  layout?: EFormLayout;
}

// Синоним для интерфейса опций.
export type TOptions = IOption<string>[];

/**
 * Выпадающий список.
 */
export class SelectBlock extends WidgetClassStateless<IProps<TOptions>> {
  static widgetName = 'Выбор из списка';

  static validator(props: IProps<TOptions>): IErrorBase[] {
    const { value: contextValue, caption, isSelectedDefault, options, layout } = props;
    let errors: IErrorBase[] = [];

    /**
     * contextValue должно быть строкой.
     */
    if (contextValue !== null && propIsStringOrNumber(contextValue)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.STRING], contextValue));
    }

    /**
     * caption должен быть строкой.
     */
    if (canBeEmptyProp(caption) && !isString(caption)) {
      errors.push(
        getWidgetPropertyError<IProps<TOptions>>(
          this.widgetName,
          'caption',
          [EPropRsType.STRING],
          caption,
        ),
      );
    }

    /**
     * isSelectedDefault должен быть логическим типом.
     */
    if (canBeEmptyProp(isSelectedDefault) && !isBoolean(isSelectedDefault)) {
      errors.push(
        getWidgetPropertyError<IProps<TOptions>>(
          this.widgetName,
          'isSelectedDefault',
          [EPropRsType.BOOLEAN],
          isSelectedDefault,
        ),
      );
    }

    /**
     * Значение options должно быть массивом объектов, состоящим из двух текстовых полей label и value.
     */
    if (isArray(options)) {
      errors.push(...validateDefaultOptions(options, this.widgetName));
    } else {
      errors.push(
        getWidgetPropertyError<IProps<TOptions>>(
          this.widgetName,
          'options',
          [EPropRsType.ARRAY],
          options,
        ),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps<TOptions>>(
          this.widgetName,
          'layout',
          [EPropRsType.STRING],
          layout,
        ),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps<TOptions>) {
    return (
      !isEqual(this.props.value, nextProps.value) ||
      this.props.caption !== nextProps.caption ||
      this.props.options !== nextProps.options ||
      this.props.isSelectedDefault !== nextProps.isSelectedDefault ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.layout !== nextProps.layout
    );
  }

  /**
   * Выбор нового значения из выпадающего списка.
   *
   * @param {number} index Индекс выбранного пункта списка.
   * @param {string} value Значение выбранного пункта списка.
   */
  onChange = (index: number, value: string) => {
    this.props.onChange(value);
  };

  render() {
    const { value, caption, options, hasError, readOnly, layout, onlyShow } = this.props;

    return options.length > 0 ? (
      <FormWidgetContainer layout={layout}>
        <Form.Item
          data-test-id="SelectBlock.Component"
          label={this.props.label ?? ' '}
          colon={false}
          required={onlyShow === true ? false : this.props.isRequired}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <Select
            data-test-id="SelectBlock.select"
            value={value}
            placeholder={caption}
            onChange={this.props.onChange}
            onBlur={this.props.onBlur}
            disabled={onlyShow || readOnly}
          >
            {options.map(el => (
              <Option data-test-id="SelectBlock.select.item" key={el.value} value={el.value}>
                {el.label}
              </Option>
            ))}
          </Select>
          <br />
        </Form.Item>
      </FormWidgetContainer>
    ) : (
      <p
        data-test-id="SelectBlock.noItem"
        className={hasError ? 'ssp-player-validation-error' : null}
      >
        {NOT_FOUND_SELECT}
      </p>
    );
  }
}
