import React, { Fragment } from 'react';

import { Checkbox, Form } from 'antd';

import isArray from 'lodash/isArray';
import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IOption, IWidgetBaseProps, WidgetClass, IErrorBase } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { NOT_FOUND_CHECKBOX } from '../Const';
import {
  getWidgetContextError,
  getWidgetPropertyError,
  validateDefaultOptions,
  propIsStringOrNumber,
} from './widgetUtils';

/**
 * Свойства группы Checkbox-ов.
 *
 * @prop {string} [label] Подпись над полем ввода.
 * @prop {IOption[]} options Список вариантов выбора.
 * @prop {Function} [onFocus] Функция вызываемая при фокусе компонента.
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps extends IWidgetBaseProps<string[]> {
  label?: string;
  options: IOption<string>[];
  onFocus?: () => void;
  readOnly?: boolean;
  layout?: EFormLayout;
}

/**
 * Группа Checkbox-ов.
 */
export class CheckboxGroupBlock extends WidgetClass<IProps, {}> {
  static widgetName = 'Флажки';

  props: IProps;

  /**
   * Валидация входящих параметров.
   *
   * @prop {IProps} props Параметры виджета для проверки.
   */
  static validator(props: IProps): IErrorBase[] {
    const { value: contextValues, options, layout } = props;

    let errors: IErrorBase[] = [];

    /**
     * Значение должно быть простым массивом, состоящим из строк.
     */
    if (contextValues !== null && !isArray(contextValues)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.ARRAY], contextValues));
    } else if (isArray(contextValues)) {
      contextValues?.some((contextValue: string, optionIndex: number) => {
        let hasError = false;
        if (propIsStringOrNumber(contextValue)) {
          errors.push(
            getWidgetContextError(
              this.widgetName,
              [EPropRsType.STRING, EPropRsType.NUMBER],
              contextValue,
              optionIndex,
            ),
          );
        }
        return hasError;
      });
    }

    /**
     * Значение options должно быть массивом объектов, состоящим из двух текстовых полей label и value.
     */
    if (isArray(options)) {
      errors.push(...validateDefaultOptions(options, this.widgetName));
    } else {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'options', [EPropRsType.ARRAY], options),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.value !== nextProps.value ||
      this.props.options !== nextProps.options ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.layout !== nextProps.layout
    );
  }

  /**
   * Переключение чекбокса при клике.
   */
  handleChange = (e: string[]) => {
    this.props.onChange(e);

    // валидируем чекбоксы после изменения значения
    setTimeout(this.props.onBlur, 0);
  };

  render() {
    const { options, hasError, value, readOnly, layout } = this.props;

    return options.length > 0 ? (
      <FormWidgetContainer layout={layout}>
        <Form.Item
          data-test-id="CheckboxGroupBlock.Component"
          required={this.props.onlyShow || readOnly ? false : this.props.isRequired}
          label={this.props.label ?? ' '}
          colon={false}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <Checkbox.Group
            style={{ width: '100%' }}
            data-test-id="CheckboxGroupBlock.checkbox"
            value={value}
            defaultValue={value}
            onChange={this.handleChange}
            disabled={this.props.onlyShow || readOnly}
          >
            {options.map((item: IOption<string>) => (
              <Fragment key={item.label + item.value}>
                <Checkbox data-test-id="CheckboxGroupBlock.checkbox.item" value={item.value}>
                  {item.label}
                </Checkbox>
                <br />
              </Fragment>
            ))}
          </Checkbox.Group>
        </Form.Item>
      </FormWidgetContainer>
    ) : (
      <p
        data-test-id="CheckboxGroupBlock.noItem"
        className={hasError ? 'ssp-player-validation-error' : null}
      >
        {NOT_FOUND_CHECKBOX}
      </p>
    );
  }
}
