import React, { FC, useEffect } from 'react';
import { addInlineStyles, removeInlineStyles } from '_app/utils';
import { IWidgetWrapper } from '_components/WidgetWrapper/interfaces';

/**
 * Обертка для добавления стилей из виджета
 *
 */
export const WidgetWrapper:FC<IWidgetWrapper> = ({widget, children}) => {
  useEffect(() => {
    const id = `styles-${widget.component}_${widget.index}`;

    if (widget?.inlineStyles) {
      addInlineStyles(widget, id)
    }
    return () => removeInlineStyles(id);
  }, [])

  return <>{children}</>
}