import escapeRegExp from 'lodash/escapeRegExp';
import get from 'lodash/get';
import uniq from 'lodash/uniq';

import { IPlayerContext } from '../Models';

/**
 * Класс для шаблонных строк.
 * Используется для реализации типа "template" при задании свойств виджета экрана.
 */
export class TemplatedString {
  // специальный флаг
  isTemplatedString: boolean = true;

  // Исходная строка-шаблон , вида 'Hello, Mr ${NAME} !{SURNAME}!'
  template: string = null;

  // Пути до значений в контексте.
  contextPaths: string[] = [];

  /**
   * Получаем путь до значений в контексте для отображения шаблонной строки.
   *
   * @param {string} template Шаблон.
   */
  getContextPaths(template: string): string[] {
    const r = new RegExp(
      '[$!]{(([a-zA-Z]\\w*)((\\.[a-zA-Z]\\w*)|(\\[\'\\S+\'])|(\\[\\"\\S+\\"])|(\\[\\d+]))*)(?=})',
      'gm',
    );
    const tokens = template.match(r);

    if (tokens === null || tokens.length === 0) {
      throw new Error('Не используются переменные.');
    }

    // Оставляем уникальные токены в строке, так как будет замена токена по всей строке.
    // Так же обрезаем первые два символа у токена "${example"
    return uniq(tokens).map(token => token.slice(2));
  }

  constructor(template: string) {
    this.template = template;
    try {
      this.contextPaths = this.getContextPaths(template);
    } catch (e) {
      throw new Error(`Произошла ошибка при разборе шаблонной строки "${template}": ${e.message}`);
    }
  }

  forReplace(char: string, token: string) {
    return new RegExp(escapeRegExp(char + '{' + token + '}'), 'gm');
  }

  /**
   * Получение значения из заданной шаблонной строки.
   *
   * @param {IPlayerContext} context Контекст с переменными для шаблона.
   */
  getValue(context: IPlayerContext) {
    let result = this.template;
    try {
      this.contextPaths.forEach((token: string) => {
        const value = get(
          context,
          token,
          new Error(`Переменной с таким именем "${token}" не существует в контексте.`),
        );

        if (value instanceof Error) {
          throw value;
        }

        if (value === null) {
          throw new Error(`У переменной с таким именем "${token}" не задано значение в контексте.`);
        }

        result = result
          .replace(this.forReplace('$', token), value)
          .replace(this.forReplace('!', token), value);
      });
    } catch (e) {
      throw new Error(
        `Не удалось подставить переменную в шаблон строки: "${this.template}". ${e.message}`,
      );
    }
    return result;
  }
}
