import React, { FC } from 'react';

import { Form } from 'antd';

import { EFormLayout } from '_app/Enums';

import { IFormWidgetContainerProps } from './interfaces';

/**
 * Контейнер для установки layout для каждого виджета
 */
export const FormWidgetContainer: FC<IFormWidgetContainerProps> = ({ layout, children }) => {
  return (
    <Form component="div" layout={layout ?? EFormLayout.VERTICAL}>
      {children}
    </Form>
  );
};
