import get from 'lodash/get';

import { connect } from 'react-redux';

import { switchToPhase } from '_actions/PlayerActions';
import {  IScriptPlayerStore } from '../Models';
import { getBlockById } from '../utils';

/**
 * Свойства компонента из redux store.
 *
 * @prop {string[]} phases Все фазы скрипта.
 * @prop {string} currentPhase Текущая фаза скрипта.
 */
export interface IStateToProps {
  phases: string[];
  currentPhase: string;
}

/**
 * Свойства компонента вызывающие действия redux.
 *
 * @prop {Function} onChangePhase Переключение фазы.
 */
export interface IPropsDispatch {
  onChangePhase: (phaseName: string) => void;
}

/**
 * Свойства компонента по переключению фаз.
 */
export interface IPhaseControlProps extends IStateToProps, IPropsDispatch {}

export const mapStateToProps = (store, ownProps): IStateToProps => {
  const { script } = get(store, ownProps.scriptName) as IScriptPlayerStore;

  let currentPhase = null;
  let phases = null;
  if (script && script.currentBlockId) {
    const { script: scriptData, currentBlockId } = script;
    phases = scriptData.phases;
    const currentBlock = getBlockById(currentBlockId, scriptData.blocks);
    currentPhase = currentBlock.phase;
  }

  return {
    phases,
    currentPhase,
  };
};

export const mapDispatchToProps = (dispatch, ownProps): IPropsDispatch => ({
  onChangePhase: (phase: string) => dispatch(switchToPhase(phase, ownProps.scriptName)),
});

/**
 * Экспортируем функцию-обёртку над компонентом, возвращающую законекченный к store компонент.
 * Возвращаемый компонент получает из store props в соответствии с интерфейсом IPhaseControlProps.
 * T - Собственные props компонента Component.
 */
export default function phaseControlWrapper<T>(Component: any) {
  return connect(mapStateToProps, mapDispatchToProps)(Component);
}
