import * as React from 'react';

import { EPropRsType } from '_app/Enums';
import { IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '_app/Models';
import { MarkdownReader } from '_components/MardownReader';

import { getWidgetPropertyError, propIsStringOrNumber } from './widgetUtils';

/**
 * Свойства текстового блока.
 *
 * @prop {string} markdown Форматированный текст, выводимый пользователю.
 */
export interface IProps extends IWidgetBaseProps<void> {
  markdown: string;
}

/**
 * Форматированный текстовый блок.
 */
export class MarkdownBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Отображение форматированного текста';

  static validator(props: IProps): IErrorBase[] {
    const { markdown } = props;

    let errors: IErrorBase[] = [];

    /**
     * markdown должно быть текстом или числом.
     */
    if (propIsStringOrNumber(markdown)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'markdown',
          [EPropRsType.STRING, EPropRsType.NUMBER],
          markdown,
        ),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return this.props.markdown !== nextProps.markdown;
  }

  render() {
    const { markdown } = this.props;

    return <MarkdownReader content={markdown} dataTestId="MarkdownBlock.Component" />;
  }
}
