import React from 'react';

import { Form } from 'antd';

import MaskedInput from 'antd-mask-input';

import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IWidgetBaseProps, WidgetClassStateless, IErrorBase } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { getWidgetContextError, getWidgetPropertyError } from './widgetUtils';

/**
 * Свойства поля ввода по маске.
 *
 * @prop {string} [label] Подпись над полем ввода.
 * @prop {string} mask Маска для ввода данных.
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps extends IWidgetBaseProps<string> {
  label?: string;
  mask: string;
  readOnly?: boolean;
  layout?: EFormLayout;
}

/**
 * Поле ввода по маске.
 */
export class MaskedInputBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Ввод по маске';

  static validator(props: IProps): IErrorBase[] {
    const { value, mask, label, layout } = props;

    let errors: IErrorBase[] = [];

    /**
     * value должен быть строкой.
     */
    if (value !== null && !isString(value)) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.STRING], value));
    }

    /**
     * label должен быть строкой.
     */
    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    /**
     * mask должен быть строкой.
     */
    if (!isString(mask)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'mask', [EPropRsType.STRING], mask),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.value !== nextProps.value ||
      this.props.label !== nextProps.label ||
      this.props.mask !== nextProps.mask ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.layout !== nextProps.layout
    );
  }

  render() {
    const {
      onlyShow,
      isRequired,
      label,
      onChange,
      value,
      mask,
      errorMessage,
      hasError,
      readOnly,
      layout,
    } = this.props;

    return (
      <FormWidgetContainer layout={layout}>
        <Form.Item
          data-test-id="MaskedInputBlock.Component"
          required={isRequired && !onlyShow}
          label={label ?? ' '}
          colon={false}
          validateStatus={hasError ? 'error' : null}
          help={errorMessage ? errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <MaskedInput
            className="ssp-player-number-block"
            value={value}
            mask={mask}
            onChange={event => onChange(event.target.value)}
            onBlur={this.props.onBlur}
            disabled={onlyShow || readOnly}
          />
        </Form.Item>
      </FormWidgetContainer>
    );
  }
}
