import { IToken } from '@sber-ssp-ui/spruce';
import * as React from 'react';

import { Button } from 'antd';

import isEqual from 'lodash/isEqual';
import isString from 'lodash/isString';

import { EPropRsType } from '_app/Enums';
import { IButtonRouteProps, WidgetClass, IErrorBase } from '_app/Models';

import { getWidgetPropertyError } from './widgetUtils';

/**
 * Свойства компонента.
 */
export interface IProps extends IButtonRouteProps {}

/**
 * Кнопка с переходом.
 */
export class ButtonRoute extends WidgetClass<IProps, void> {
  static widgetName = 'Кнопка с переходом';

  props: IProps;

  static validator(props: IProps): IErrorBase[] {
    const { disabledExpression, interpreter, label } = props;
    let errors: IErrorBase[] = [];

    /**
     * label должен быть строкой.
     */
    if (label !== null && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    if (disabledExpression && disabledExpression.expression) {
      try {
        interpreter.interpret(disabledExpression.expression as IToken);
      } catch (e) {
        errors.push({
          title: `В виджете "${this.widgetName}" произошла ошибка при вычислении выражения для статуса недоступности кнопки.`,
          text: e.message,
        });
      }
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.interpreter !== nextProps.interpreter ||
      this.props.disabledExpression !== nextProps.disabledExpression ||
      !isEqual(this.props.playerContext, nextProps.playerContext) ||
      !isEqual(this.props.nextId, nextProps.nextId) ||
      !isEqual(this.props.label, nextProps.label)
    );
  }

  handleClick = () => {
    const { onSwitchToBlock, nextId, shouldSave, shouldValidate, onValidateClick } = this.props;

    if (!shouldValidate) {
      onSwitchToBlock(nextId, shouldSave);
      return;
    }

    const isValidate = onValidateClick(null, true);

    if (shouldValidate && isValidate) {
      onSwitchToBlock(nextId, shouldSave);
    }
  };

  render() {
    const { disabledExpression, label, interpreter } = this.props;
    let disabled = false;

    if (disabledExpression && disabledExpression.expression) {
      disabled = !!interpreter.interpret(disabledExpression.expression as IToken);
    }

    return (
      <Button
        data-test-id="ButtonRoute.button"
        type="primary"
        onClick={this.handleClick}
        disabled={disabled}
      >
        {label}
      </Button>
    );
  }
}
