import React from 'react';

import { Form, DatePicker } from 'antd';
import dayjs from 'dayjs';
import moment, { Moment } from 'moment';

import locale from 'antd/lib/date-picker/locale/ru_RU';

import isString from 'lodash/isString';

import { EPropRsType } from '_app/Enums';
import { IErrorBase, WidgetClassStateless } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';
import {
  getEnumError,
  getEnumKeys,
  getWidgetContextError,
  getWidgetPropertyError,
} from '_components/widget/widgetUtils';

import { EDateRangeInputTypes } from './constants';
import { IProps } from './interfaces';

/**
 * Компонент выбора диапазона дат.
 */
export class DateRangeBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Выбор диапазона дат';

  static validator(props: IProps): IErrorBase[] {
    const { value, label, rangeType, format, layout } = props;

    let errors: IErrorBase[] = [];

    function validatePropDate(nameProp: keyof IProps, prop: IProps, widgetName: string) {
      const valueProp = prop[nameProp] as string;
      if (canBeEmptyProp(valueProp) && !dayjs(valueProp).isValid()) {
        errors.push(
          getWidgetPropertyError<IProps>(widgetName, nameProp, [EPropRsType.OBJECT], valueProp),
        );
      }
    }

    /**
     * Значение start и end должно быть датой.
     */
    const isStartValid = Boolean(value?.start) && dayjs(value.start).isValid();
    const isEndValid = Boolean(value?.end) && dayjs(value.end).isValid();

    if (value !== null && !isStartValid && !isEndValid) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.OBJECT], value));
    }

    /**
     * min должно быть датой.
     */
    validatePropDate('min', props, this.widgetName);

    /**
     * max должно быть датой.
     */
    validatePropDate('max', props, this.widgetName);

    /**
     * label должен быть строкой.
     */
    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    /**
     * format должен быть строкой.
     */
    if (canBeEmptyProp(format) && !isString(format)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'format', [EPropRsType.STRING], format),
      );
    }

    const availableDateTypes = getEnumKeys(EDateRangeInputTypes);

    /**
     * dateType должен быть строкой.
     */
    if (!isString(rangeType)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'rangeType',
          [EPropRsType.STRING],
          rangeType,
        ),
      );
    } else if (availableDateTypes.indexOf(rangeType) < 0) {
      errors.push(
        getEnumError<IProps>(availableDateTypes, rangeType, this.widgetName, 'rangeType'),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  getDisabledDate = (d: Moment): boolean => {
    const { min, max } = this.props;
    return !d || (max ? d.isAfter(max) : false) || (min ? d.isSameOrBefore(min) : false);
  };

  /**
   * Колбэк на изменение выбора дат
   *
   * @param {[Moment, Moment]} date - дата из RangePicker
   */
  onChange = (date: [Moment, Moment]) => {
    const start = date && date[0] ? date[0].toDate() : null;
    const end = date && date[1] ? date[1].toDate() : null;

    const value = date?.length > 0 ? { start, end } : null;
    this.props.onChange(value);

    setTimeout(this.props.onBlur, 0);
  };

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.value !== nextProps.value ||
      this.props.label !== nextProps.label ||
      this.props.min !== nextProps.min ||
      this.props.max !== nextProps.max ||
      this.props.rangeType !== nextProps.rangeType ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.format !== nextProps.format ||
      this.props.layout !== nextProps.layout
    );
  }

  /**
   * Трансформация типа даты из строки в Enum предназначенный для RangePicker.
   */
  transformType = (rangeType: string): 'month' | 'week' | 'year' | 'date' => {
    switch (rangeType) {
      case EDateRangeInputTypes.MONTH:
        return 'month';
      case EDateRangeInputTypes.WEEK:
        return 'week';
      case EDateRangeInputTypes.YEAR:
        return 'year';
      case EDateRangeInputTypes.DAY:
      case EDateRangeInputTypes.DAY_TIME:
        return 'date';
      default:
        return 'date';
    }
  };

  /**
   * Трансформация value из пропсов в формат для RangePicker
   */
  getValue = (): [Moment, Moment] => {
    const value = this.props.value;
    const start = value?.start ? moment(value.start) : null;
    const end = value?.end ? moment(value.end) : null;

    return value ? [start, end] : null;
  };

  render() {
    const { rangeType, format } = this.props;

    const showTime = rangeType === EDateRangeInputTypes.DAY_TIME;

    return (
      <FormWidgetContainer layout={this.props.layout}>
        <Form.Item
          data-test-id="DateRangeBlock.Component"
          required={this.props.onlyShow === true ? false : this.props.isRequired}
          label={this.props.label ?? ' '}
          colon={false}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <DatePicker.RangePicker
            data-test-id="DateRangeBlock.date"
            value={this.getValue()}
            disabledDate={this.getDisabledDate}
            onChange={this.onChange}
            format={format}
            showTime={showTime}
            picker={this.transformType(rangeType)}
            disabled={this.props.onlyShow || this.props.readOnly}
            locale={locale}
            className="ssp-player-date-range"
          />
        </Form.Item>
      </FormWidgetContainer>
    );
  }
}
