import { fetchApi } from '../index';
import { IMarketPlace } from '_app/Models';

/**
 *
 * @prop {string[]} marketPlaceList Массив урлов маркетплейсов.
 *
 * @returns {Promise[]} массив выполненных промисов
 */
export const fetchLoadMarketPlaceApi = async (marketPlaceList: IMarketPlace[]) => {
  const arrayPromiseList = marketPlaceList.map(({ baseUrl }) => fetchApi(`${baseUrl}list/`));

  return Promise.allSettled(arrayPromiseList);
};
