import React, { FC } from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';

import { replaceTagsForMarkdown } from '_components/widget/widgetUtils';

import { MarkdownReaderProps } from './interfaces';

/**
 * Компонент выводит содержимое маркдауна
 */
export const MarkdownReader: FC<MarkdownReaderProps> = ({
  content,
  dataTestId = 'MarkdownReader',
}) => {
  const renderContent = content ? replaceTagsForMarkdown(content).toString() : '';

  return (
    <ReactMarkdown
      data-test-id={dataTestId}
      remarkPlugins={[remarkGfm]}
      className="ssp-player-markdown-editor"
    >
      {renderContent}
    </ReactMarkdown>
  );
};
