/**
 * Свойства компонента из redux store.
 *
 * @prop {string} currentScriptKey Ключ для скрипта.
 * @prop {IScriptStore} scriptStore Данные о скрипте из store.
 * @prop {IScriptBlockError} playerError Ошибка работы плеера.
 * @prop {number} startScreenTime Время начала отображения текущего экрана.
 * @prop {IVersionInfo} versionInfo Информация по версии.
 * @prop {Record<string, ICustomWidgetData>} customWidgetMap Карта кастомных виджетов.
 */
export interface IStateToProps {
  scriptStore: IScriptStore | null;
  playerError: IScriptBlockError | null;
  startScreenTime: number | null;
  versionInfo: IVersionInfo | null;
  customWidgetMap: Record<string, ICustomWidgetData> | null;
}

/**
 * Свойства компонента вызывающие действия redux.
 *
 * @prop {Function} getAndStartScript Получение скрипта.
 * @prop {Function} updateContextDiffAction Изменение части контекста плеера.
 * @prop {Function} goBackAction Возврат на предыдущий экран с восстановлением контекста.
 * @prop {Function} goNextAction Переключить экран на следующий.
 * @prop {Function} showPlayerError Показать ошибку плеера.
 * @prop {Function} showBlockErrors Показать ошибки обработки блока.
 * @prop {Function} showCalculateError Показать ошибку вычисления выражений.
 * @prop {Function} addBMData Добавить данные для Бизнес Мониторинга.
 * @prop {Function} onSwitchToBlock Переход к блоку скрипта.
 * @prop {Function} getVersionAction Получение версии скрипта.
 * @prop {Function} saveScriptHistoryAction Сохраняем историю скрипта во внешнюю функцию.
 * @prop {Function} fetchMarketPlaceListAction Запрос на список маркетплейсов
 * @prop {Function} validateMarketPlaceWidgetsAction Проверка наличия всех кастомных виджетов в карте
 * @prop {Function} clearScriptDataAction Очистка данных скрипта
 */
export interface IPropsDispatch {
  getAndStartScript: () => void;
  updateContextDiffAction: (diff: IPlayerContextDiff, shouldUpdateInterpreter?: boolean) => void;
  goBackAction: () => void;
  goNextAction: () => void;
  showPlayerError: (scriptPlayerError: IError) => void;
  showBlockErrors: (scriptBlockErrors: IError[]) => void;
  showCalculateError: (scriptCalculateError: IError) => void;
  addBMData: (businessMonitoring: IBusinessMonitoring) => void;
  onSwitchToBlock: (blockId: string, saveContext?: boolean) => void;
  getVersionAction: () => void;
  saveScriptHistoryAction: () => void;
  fetchMarketPlaceListAction: (marketPlaceList: IMarketPlace[]) => void;
  validateMarketPlaceWidgetsAction: () => void;
  clearScriptDataAction: () => void;
}

/**
 * Свойства компонента.
 */
export interface IProps extends IPlayerProps, IStateToProps, IPropsDispatch {}

/**
 * Состояние компонента.
 *
 * @prop {Partial<IBackendError>[]} globalErrors Глобальная ошибка для вывода пользователю.
 * @prop {IValidationError[]} validationErrors Ошибки валидации.
 */
export interface IState {
  globalErrors?: Partial<IBackendError>[];
  validationErrors?: IValidationError[];
  isLoadedCustomWidget: boolean;
}

/**
 * Проигрыватель скриптов.
 */
export class Player extends React.Component<IProps, IState> {
  // Чтоб работало автодополнение в IDE.
  props: IProps;

  state = {
    globalErrors: [],
    validationErrors: [],
    isLoadedCustomWidget: false,
  };

  /**
   * Отправка ошибки в родительский компонент, с последним переданным результатом, иначе при обновлении происходит многократная отправка.
   * http://jira.ca.sbrf.ru/browse/BPSBSCP-667
   */
  handleOnError: (errors: (IScriptBlockError | IScriptCalculateError)[]) => void =
    this.props.onError && debounce(this.props.onError, TIME_DEBOUNCE);

  shouldComponentUpdate(nextProps: IProps, nextState: IState) {
    return (
      // Изменение данных плеера.
      !isEqual(this.state, nextState) ||
      !isEqual(this.props.scriptStore, nextProps.scriptStore) ||
      !isEqual(this.props.script, nextProps.script) ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.onlyShowNextLabel !== nextProps.onlyShowNextLabel ||
      this.props.playerError !== nextProps.playerError ||
      this.props.SectionComponent !== nextProps.SectionComponent ||
      this.props.scriptStore?.playerContext !== nextProps.scriptStore.playerContext
    );
  }

  async componentDidMount() {
    await this.initScript(this.props);

    this.setState({ isLoadedCustomWidget: true });
  }

  componentDidUpdate(prevProps: IProps) {
    const { scriptStore, onError, onPhaseChange, outsideContext, script } = prevProps;

    if (!scriptStore || !this.props.scriptStore) return;

    // если изменился статус скрипта на IDLE, то переинициализируем скрипт
    if (scriptStore.status !== EStatus.IDLE && this.props.scriptStore.status === EStatus.IDLE) {
      return this.initScript(this.props);
    }

    // если был выбран другой скрипт когда компонент не был отключен из DOM дерева
    if (!isEqual(script, this.props.script)) {
      return this.props.getAndStartScript();
    }

    const errors = this.checkForErrors(this.props);

    if (errors.length > 0) {
      onError && this.handleOnError(errors);
      return;
    }

    // Callback на изменение фазы onPhaseChange
    if (this.props.scriptStore.script.blocks && scriptStore.script.blocks) {
      const {
        currentBlockId,
        script: { blocks },
      } = scriptStore;
      const {
        scriptStore: {
          currentBlockId: nextCurrentBlockId,
          script: { blocks: nextBlocks, phases },
        },
      } = this.props;
      // Проверяем что у скрипта вообще есть фазы
      if (phases && phases.length > 0) {
        const currentBlock = getBlockById(currentBlockId, blocks);
        const nextCurrentBlock = getBlockById(nextCurrentBlockId, nextBlocks);
        const bothBlocksHavePhase =
          currentBlock && nextCurrentBlock && currentBlock.phase !== nextCurrentBlock.phase;
        const newBlockHasPhase = !currentBlock && nextCurrentBlock && nextCurrentBlock.phase;
        // проверяем, что происходит либо переключение между блоками разных фаз либо первый переход на блок с фазой в скрипте
        if (bothBlocksHavePhase || newBlockHasPhase) {
          onPhaseChange && onPhaseChange(nextCurrentBlock.phase);
        }
      }
    }

    // Завершение скрипта
    if (
      this.props.scriptStore.currentBlockId !== null && // Скрипт должен быть инициализирован.
      this.props.scriptStore.currentBlockId !== scriptStore.currentBlockId // Должен придти следующий блок.
    ) {
      const {
        scriptStore: { currentBlockId, script, playerContext },
        onResult,
        script: { outputContext },
      } = this.props;
      const block = getBlockById(currentBlockId, script.blocks);

      if (block.type === EBlockType.STOP) {
        const resultContext = this.getResultContext(playerContext, outputContext);

        if (resultContext) {
          onResult(resultContext, EResultType.SUCCESS);
          this.props.getAndStartScript();
        }
      }
    }

    // Обработка нового внешнего контекста для плеера и новых режимов инициализации из вне,
    // когда компонент не был отключен из DOM дерева.
    if (outsideContext !== this.props.outsideContext) {
      this.updateContext(this.props, this.state);
    }
  }

  /**
   * Функция проверки и фильтрации выходного контекста
   *
   * @param {IPlayerContext} playerContext Текущий контекст скрипта.
   * @param {IContextParam[]} [outputContext] Описание выходных параметров скрипта.
   *
   * @returns {IPlayerContext|void} Итоговый контекст или выброс ошибки.
   */
  getResultContext(
    playerContext: IPlayerContext,
    outputContext?: IContextParam[],
  ): IPlayerContext | void {
    if (!outputContext?.length) {
      return playerContext;
    }

    const resultContext = {};
    const requiredContextErrors: string[] = [];

    outputContext.forEach(({ name, required, type, default: defaultValue }) => {
      // если элемент required, но равен null или undefined
      if (required && playerContext[name] == null) {
        requiredContextErrors.push(name);
        return;
      }

      // если элемент не обязателен и равен null или undefined
      if (!required && playerContext[name] == null) {
        // если defaultValue не пустая строка, то конвертируем, иначе null
        resultContext[name] = defaultValue ? convertSimpleValue(defaultValue, type) : null;
        return;
      }

      resultContext[name] = cloneDeep(playerContext[name]);
    });

    if (requiredContextErrors.length) {
      this.props.showPlayerError(
        preparePlayerErrorExpected({
          title: 'Ошибка при проверке итогового контекста.',
          text: `Отсутствует обязательное значение на выходе для переменн${
            requiredContextErrors.length === 1 ? 'ой' : 'ых'
          } ${requiredContextErrors.join(', ')}.`,
        }),
      );

      return;
    }

    return resultContext;
  }

  /**
   * Проверка наличия компонента скрипта в дополнительных компонентах плеера.
   *
   * @param {IProps} props пропсы
   */
  validateCustomWidgets = (props: IProps) => {
    const duplicate = this.getDuplicateCustomWidgets(props.customWidgets);

    if (duplicate.length > 0) {
      props.showPlayerError(
        preparePlayerErrorExpected({
          text: `В плеер переданы дополнительные виджеты с одинаковыми названиями: "${duplicate.join(
            '", "',
          )}".`,
        }),
      );
    }

    const nextBlocks = this.props.scriptStore?.script?.blocks;

    if (nextBlocks) {
      // Разбираем скрипт и проверяем есть ли в дистрибутиве нужные дополнительные компоненты для отображения.
      nextBlocks
        .filter(blockItem => blockItem.type === EBlockType.SCREEN)
        .forEach((blockItem: IBlockScreen) => {
          blockItem.widgets.forEach((widget: IWidget<null> | ICustomWidget<null>) => {
            if ('componentType' in widget && widget.componentType === EComponentType.MARKETPLACE) {
              return;
            }

            this.validateCustomWidgetClass(widget.component);
          });
        });
    }
  };

  /**
   * Проверяем есть ли в дистрибутиве кастомные компоненты указанные в скрипте.
   *
   * @param {EDefaultWidget | string} widgetName Название компонента из скрипта.
   */
  validateCustomWidgetClass = (widgetName: EDefaultWidget | string): void => {
    const { customWidgets, showPlayerError } = this.props;
    const NOT_FOUND = `Не найден дополнительный виджет с названием "${widgetName}" для отображения.`;

    if (isKnownWidget(widgetName)) {
      return;
    }

    if (!customWidgets) {
      showPlayerError(
        preparePlayerErrorExpected({
          text: `${NOT_FOUND} В плеер не подключены дополнительные виджеты для отображения.`,
        }),
      );
      return;
    }

    const customWidgetNames = this.getCustomWidgetNames(customWidgets);

    if (customWidgetNames.indexOf(widgetName as string) < 0) {
      const AVAILABLE_WIDGETS = customWidgetNames.join('", "');
      showPlayerError(
        preparePlayerErrorExpected({
          text: `${NOT_FOUND} В плеер подключены следующие дополнительные виджеты для отображения: "${AVAILABLE_WIDGETS}".`,
        }),
      );
    }
  };

  /**
   * Получить названия дополнительных компонентов.
   *
   * @param {IWidgetDefinition<any>[]} customWidgets Дополнительные компоненты.
   */
  getCustomWidgetNames = (customWidgets: IWidgetDefinition<any>[] = []): string[] =>
    customWidgets.map(w => w.name || w.component.displayName);

  /**
   * Получаем список дублированных названий в дополнительных компонентах.
   *
   * @param {IWidgetDefinition<any>[]} customWidgets Дополнительные виджеты.
   */
  getDuplicateCustomWidgets = (customWidgets: IWidgetDefinition<any>[]): string[] => {
    const widgetNamesCount = this.getCustomWidgetNames(customWidgets).reduce((widgets, current) => {
      if (widgets[current]) {
        widgets[current]++;
      } else {
        widgets[current] = 1;
      }
      return widgets;
    }, {});
    return Object.keys(widgetNamesCount).filter(key => {
      return widgetNamesCount[key] > 1;
    });
  };

  /**
   * Проверка скрипта на ошибки.
   *
   * @param {IProps} props Пропсы плеера для проверки
   */
  checkForErrors = (props: IProps): (IScriptBlockError | IScriptCalculateError)[] => {
    let errors: (IScriptBlockError | IScriptCalculateError)[] = [];

    // Ошибка при запросе или обработки данных с сервера, кроме ошибок от BH
    if (
      props.scriptStore.error !== this.props.scriptStore.error &&
      props.scriptStore.error &&
      props.scriptStore.error.errorType !== EErrorType.FROM_SERVER
    ) {
      errors.push(props.scriptStore.error);
    }
    // Ошибка плеера
    if (props.playerError !== this.props.playerError) {
      errors.push(props.playerError);
    }
    // Ошибка при обработке блоков
    if (
      props.scriptStore.scriptBlockErrors.length > 0 &&
      !isEqual(props.scriptStore.scriptBlockErrors, this.props.scriptStore.scriptBlockErrors)
    ) {
      props.scriptStore.scriptBlockErrors.forEach(scriptBlockError => {
        errors.push(scriptBlockError);
      });
    }
    // Ошибка при вычислении выражений
    if (
      props.scriptStore.scriptCalculateErrors.length > 0 &&
      !isEqual(
        props.scriptStore.scriptCalculateErrors,
        this.props.scriptStore.scriptCalculateErrors,
      )
    ) {
      props.scriptStore.scriptCalculateErrors.forEach(scriptCalculateError => {
        errors.push(scriptCalculateError);
      });
    }
    return errors.filter(error => error !== null);
  };

  /**
   * Прокрутка к первой ошибке валидации.
   */
  scrollToFirstError = () => {
    // если ошибок нет,то ничего не делаем
    if (this.state.validationErrors.length === 0) {
      return;
    }
    const thisDOMElement = findDOMNode(this) as Element;
    if (thisDOMElement) {
      // альтернативно можно проверять наличие в state ошибок на момент вызова скролла
      const target = thisDOMElement.querySelector(`.${VALIDATION_ERROR_MARKER_CLASS}`);
      if (target) {
        target.scrollIntoView();
      }
    }
  };

  /**
   * Режим инициализации скрипта.
   *
   * @param {IProps} props пропсы
   */
  initScript = async (props: IProps) => {
    const {
      getAndStartScript,
      scriptStore,
      script,
      marketplaces,
      fetchMarketPlaceListAction,
      validateMarketPlaceWidgetsAction,
      customWidgetMap,
    } = props;

    // Если скрипт ещё не загружен или был сброшел, то загружаем скрипт
    if (
      !scriptStore ||
      scriptStore.status === EStatus.IDLE ||
      script.subject !== scriptStore.script.subject
    ) {
      getAndStartScript();

      if (marketplaces?.length && !customWidgetMap) {
        // загружаем карту кастомных виджетов из полученных маркетплейсов
        await fetchMarketPlaceListAction(marketplaces);
      }

      // проверяем наличие кастомных виджетов для скрипта, переданных пользователем
      this.validateCustomWidgets(props);
      // проверяем наличие кастомных виджетов для скрипта в маркетплейсах
      validateMarketPlaceWidgetsAction();
    }
  };

  /**
   * Обновляем контекст скрипта при изменении внешнего контекста
   *
   * @param {IProps} props Пропсы компонента.
   * @param {IState} state Состояние компонента.
   */
  updateContext = (props: IProps, state: IState) => {
    const {
      scriptStore: { status, playerContext },
      updateContextDiffAction,
      outsideContext,
    } = props;

    if (status === EStatus.SUCCESS && outsideContext && !this.hasErrors(props, state)) {
      updateContextDiffAction({ ...playerContext, ...outsideContext });
    }
  };

  /**
   * Очищаем информацию о контексте, который является невидимым на текущем экране, при этом если значение поменялось,
   * и оно есть на предыдущей странице, сохраняем предыдущее значение, вместо нового.
   */
  clearInvisibleContext = () => {
    const {
      scriptStore: {
        currentBlockId,
        history,
        script: { blocks },
        visibleScreenBlock,
      },
      updateContextDiffAction,
    } = this.props;
    const currentBlock = getBlockById(currentBlockId, blocks) as IBlockScreen;

    // Получаем список названий свойств контекста из блока.
    const getPropertyNames = (block: IBlockScreen): string[] =>
      block.widgets.filter(widget => widget.contextName).map(widget => widget.contextName);

    const allScreenContextProperty: string[] = getPropertyNames(currentBlock);
    const visibleContextProperty: string[] = getPropertyNames(visibleScreenBlock);

    const invisibleContextProperty: string[] = difference(
      allScreenContextProperty,
      visibleContextProperty,
    );

    if (invisibleContextProperty.length > 0) {
      const previousContextPlayer =
        history.length > 0 ? last<IHistoryContext>(history).playerContext : {};

      // Изменяемая часть контекста
      const diff: IPlayerContextDiff = {};

      invisibleContextProperty.forEach((contextProperty: string) => {
        // Если информации на предыдущей странице нет о свойстве контекста, то обнуляем.
        let newValue;
        if (isUndefined(previousContextPlayer[contextProperty])) {
          newValue = null;
          // Иначе сохраняем предыдущее значение.
        } else {
          newValue = previousContextPlayer[contextProperty];
        }
        diff[contextProperty] = newValue;
      });

      updateContextDiffAction(diff);
    }
  };

  /**
   * Проверка всех виджетов на экране.
   */
  validateAllWidgets = () => {
    const validationErrors = [];
    this.props.scriptStore.visibleScreenBlock.widgets.forEach(widget => {
      const validationMsg = this.validateWidget(widget);
      if (validationMsg) {
        validationErrors.push({
          message: validationMsg,
          source: widget,
        });
      }
    });
    return validationErrors;
  };

  /**
   * Переход на следующий экран.
   */
  handleClickNext = (event: SyntheticEvent | null | MouseEvent, isNeedCustomNextAction = false) => {
    // TODO: Восстановить бизнес мониторинг, когда он появится
    const {
      scriptStore: { interpreter, visibleScreenBlock },
    } = this.props;

    this.clearInvisibleContext();

    // Валидируем экран
    let validationErrors = this.validateAllWidgets();

    // Валидируем доп. правила на кнопке
    const nextValidators = visibleScreenBlock.nextValidators || [];
    nextValidators.forEach(validator => {
      let result;
      try {
        result = interpreter.interpret(validator.expression as IToken);
      } catch (e) {
        this.props.showCalculateError(
          prepareCalculateErrorExpected({
            title: 'Ошибка при вычислении правил валидации кнопки "Далее".',
            blockId: visibleScreenBlock.id,
            additionalId: validator.message,
            message: e.message,
            stack: e.stack,
          }),
        );
      }
      if (result === false) {
        validationErrors.push({
          message: validator.message,
          source: VALIDATION_SOURCE_NEXT,
        });
      }
    });

    // Выставляем ошибки и скроллим к первой
    this.setState({ validationErrors }, () => this.scrollToFirstError());

    // Если ошибок нет осуществляем переход далее.
    if (isEmpty(validationErrors) && !isNeedCustomNextAction) {
      this.props.goNextAction();
      return true;
    }

    if (isEmpty(validationErrors) && isNeedCustomNextAction) {
      return true;
    }

    if (validationErrors.length > 0 && isNeedCustomNextAction) {
      return false;
    }

    return false;
  };

  /**
   * Обработка перехода (с кнопки) для которой включена валидация.
   *
   * @param {IWidget<IButtonRouteProps>} widget Виджет кнопки.
   */
  handleValidatedSwitch =
    (widget: IWidget<IButtonRouteProps>) => (blockId: string, saveContext?: boolean) => {
      const {
        scriptStore: { interpreter, visibleScreenBlock },
      } = this.props;

      let validationErrors = [];

      if (widget.togglesValidation) {
        validationErrors = validationErrors.concat(this.validateAllWidgets());
      }

      const validators = widget.validators || [];
      validators.forEach(validator => {
        let result;
        try {
          result = interpreter.interpret(validator.expression as IToken);
        } catch (e) {
          this.props.showCalculateError(
            prepareCalculateErrorExpected({
              title: `Ошибка при вычислении правил валидации кнопки с переходом "${widget.props.label}".`,
              blockId: visibleScreenBlock.id,
              additionalId: validator.message,
              message: e.message,
              stack: e.stack,
            }),
          );
        }
        if (result === false) {
          validationErrors.push({
            message: validator.message,
            source: widget,
          });
        }
      });

      this.setState({ validationErrors }, () => this.scrollToFirstError());

      if (!isEmpty(validationErrors)) {
        return;
      } else {
        this.props.onSwitchToBlock(blockId, saveContext);
      }
    };

  /**
   * Переход на предыдущий экран.
   */
  handleClickPrev = () => {
    this.props.goBackAction();
  };

  /**
   * Обновление значения компонента.
   *
   * @param {string} contextName Название контекста.
   * @param {any} value Значение контекста.
   * @param {string} [calculatingBlockId] Идентификатор блока расчёта.
   */
  handleChangeWidget = (contextName: string, value: any, calculatingBlockId?: string) => {
    const {
      updateContextDiffAction,
      scriptStore: {
        script: { blocks },
        interpreter,
        currentBlockId,
      },
      showCalculateError,
      showBlockErrors,
      saveScriptHistoryAction,
    } = this.props;

    const diff: IPlayerContextDiff = {};
    diff[contextName] = value;

    executeAsync(
      () => interpreter.updateContextVariable(contextName, value),
      (error: Error) =>
        showBlockErrors([
          prepareCalculateErrorExpected({
            title: 'Ошибка при обновлении контекста для интерпретатора.',
            message: error.message,
            stack: error.stack,
            blockId: currentBlockId,
            additionalId: contextName,
          }),
        ]),
    );

    if (calculatingBlockId) {
      const block = getBlockById(calculatingBlockId, blocks) as IBlockCalculation;

      if (block && block.type === EBlockType.CALC) {
        // Убираем выражение для пересчёта, который поменял пользователь.
        block.calculations
          .filter(calc => calc.contextName !== contextName)
          .forEach((calculation: ICalculation) => {
            executeAsync(
              () => {
                const result = interpreter.interpret(calculation.expression as IToken);
                diff[calculation.contextName] = result;
                interpreter.updateContextVariable(calculation.contextName, result);
              },
              (error: Error) =>
                showCalculateError(
                  prepareCalculateErrorExpected({
                    title: 'Ошибка при динамическом пересчёте.',
                    message: error.message,
                    stack: error.stack,
                    blockId: block.id,
                    additionalId: calculation.contextName,
                  }),
                ),
            );
          });
      }
    }

    updateContextDiffAction(diff, false);

    // сохраняем историю скрипта, если передана пользовательская функция сохранения
    saveScriptHistoryAction && saveScriptHistoryAction();
  };

  /**
   * Обработка потери фокуса с виджета.
   *
   * @param {IWidget} widget Компонент.
   */
  handleBlurWidget = (widget: IWidget<any>) => {
    const validationMsg = this.validateWidget(widget);
    let validationErrors = this.state.validationErrors.filter(err => err.source !== widget);
    if (validationMsg) {
      validationErrors.push({
        message: validationMsg,
        source: widget,
      });
    }
    this.setState({ validationErrors });
  };

  /**
   * Проверка виджета на обязательность заполнения.
   *
   * @param {IWidget} widget Виджет для проверки.
   */
  hasEmptyValue(widget: IWidget<any>): boolean {
    const {
      scriptStore: { playerContext },
    } = this.props;
    const value = get(playerContext, widget.contextName);

    const isEmptyPrimitive =
      isUndefined(value) || value === null || (typeof value === 'string' && value.trim() === '');
    const isEmptyArray = isArray(value) && isEmpty(value);

    // маскированный ввод считается незаполненным, пока он не введён до конца.
    let isNotFullMaskedValue = false;
    if (widget.component === EDefaultWidget.MASKED_INPUT_BLOCK) {
      let mask = widget.props.mask;
      if (mask instanceof ContextPath || mask instanceof TemplatedString) {
        mask = mask.getValue(playerContext);
      }
      isNotFullMaskedValue = isString(value) && mask.length !== value?.length;
    }

    return isEmptyPrimitive || isEmptyArray || isNotFullMaskedValue;
  }

  /**
   * Валидирует виджет.
   * Возвращает сообщение об ошибке или null.
   *
   * @param {IWidget} widget Виджет для валидации.
   */
  validateWidget = (widget: IWidget<any>): string => {
    if (widget.component === EDefaultWidget.BUTTON_ROUTE) {
      return null;
    }

    const {
      scriptStore: { interpreter, visibleScreenBlock },
    } = this.props;
    const REQUIRED_MSG = 'Поле обязательно для заполнения.';
    let message = null;

    const isReadOnlyWidget = widget?.readOnly?.some(item =>
      item?.expression ? interpreter.interpret(item.expression as IToken) : false,
    );
    // Если поле виджета обязательное, то проверяем его на заполненность.
    if (widget.isRequired && !isReadOnlyWidget) {
      // FIXME: отдельные проверки по виджетам. Как минимум для маскированного ввода.
      if (this.hasEmptyValue(widget)) {
        message = REQUIRED_MSG;
        return message;
      }
    }

    // Проверяем корректность введенного адреса электронной почты
    const NOT_VALID_EMAIL = 'Введите корректный адрес электронной почты.';
    if (widget.component === EDefaultWidget.EMAIL_INPUT_BLOCK) {
      const {
        scriptStore: { playerContext },
      } = this.props;
      const value = get(playerContext, widget.contextName);
      if (!isReadOnlyWidget && !validateEmail(value.toString().trim())) {
        message = NOT_VALID_EMAIL;
        return message;
      }
    }

    const validators = widget.validators || [];
    // Если заданы валидаторы, то проверяем правила по одному, до первого нарушенного
    validators.some(validator => {
      let result;
      try {
        result = interpreter.interpret(validator.expression as IToken);
      } catch (e) {
        this.props.showCalculateError(
          prepareCalculateErrorExpected({
            title: 'Ошибка при вычислении правил валидации виджета.',
            blockId: visibleScreenBlock.id,
            additionalId: validator.message,
            message: e.message,
            stack: e.stack,
          }),
        );
        // можно сразу остановить цикл
        return true;
      }
      if (result === false) {
        message = validator.message;
        return true;
      } else {
        return false;
      }
    });

    return message;
  };

  /**
   * Отображаем ошибки плеера.
   */
  renderErrors = () => {
    const {
      scriptStore: { scriptBlockErrors, scriptCalculateErrors, error, playerContext, history },
      playerError,
      onResult,
      versionInfo,
      clearScriptDataAction,
    } = this.props;

    const onExit = () => onResult(playerContext, EResultType.ERROR);
    const onReload = () => {
      clearScriptDataAction();
      this.initScript(this.props);
    };

    const { globalErrors } = this.state;

    let errors = [].concat(globalErrors).concat(scriptBlockErrors).concat(scriptCalculateErrors);
    error && errors.push(error);
    playerError && errors.push(playerError);

    return (
      <PlayerErrors
        onExit={onExit}
        onReload={onReload}
        errors={errors}
        playerContext={playerContext}
        history={history}
        versionInfo={versionInfo}
      />
    );
  };

  /**
   * Асинхронная обработка вывода ошибок блока.
   * Иначе вызывается в стадии render, что приводит к ошибкам (При использовании компонента фаз).
   *
   * @param {IError[]} errors Ошибки из блока.
   */
  handleShowErrors = (errors: IError[]) => {
    setTimeout(this.props.showBlockErrors, 0, errors);
  };

  /**
   * Проверка на наличие ошибок в скрипте.
   *
   * @param {IProps} props Свойства компонента.
   * @param {IState} state Состояние компонента.
   */
  hasErrors = (props: IProps, state: IState): boolean => {
    const {
      scriptStore: { scriptBlockErrors, scriptCalculateErrors, status },
      playerError,
    } = props;
    const { globalErrors } = state;
    return (
      status === EStatus.ERROR ||
      playerError !== null ||
      scriptBlockErrors.length > 0 ||
      scriptCalculateErrors.length > 0 ||
      globalErrors.length > 0
    );
  };

  render() {
    const {
      scriptStore,
      onSwitchToBlock,
      versionInfo,
      onlyShow,
      onlyShowNextLabel,
      customWidgetMap,
      customWidgets,
      scriptName,
      showCalculateError,
      SectionComponent,
    } = this.props;
    if (!scriptStore) return null;

    const { playerContext, currentBlockId, history, status, interpreter, visibleScreenBlock } =
      scriptStore;

    const { validationErrors, isLoadedCustomWidget } = this.state;

    // Из всей истории переходов между блоками, вытаскиваем блоки только экранов.
    const screenBlockHistory = history.filter(
      historyItem => historyItem.blockType === EBlockType.SCREEN,
    );

    const showLoader = currentBlockId === null || status === EStatus.LOADING;

    if (this.hasErrors(this.props, this.state)) {
      // отображаем ошибку
      return this.renderErrors();
    }

    if (!isLoadedCustomWidget || showLoader) {
      // отображаем загрузку
      return <Spin style={{ display: 'flex', justifyContent: 'center' }} />;
    }

    if (
      visibleScreenBlock === null ||
      (visibleScreenBlock && visibleScreenBlock.type !== EBlockType.SCREEN)
    ) {
      return null;
    }

    // Текущий блок - это экран
    return (
      <Screen
        playerContext={cloneContext(playerContext)}
        onSwitchToBlock={onSwitchToBlock}
        onValidatedSwitchToBlock={this.handleValidatedSwitch}
        hasHistory={screenBlockHistory.length > 0}
        block={visibleScreenBlock}
        customWidgets={customWidgets}
        onChangeWidget={this.handleChangeWidget}
        onBlurWidget={this.handleBlurWidget}
        onClickPrev={this.handleClickPrev}
        onClickNext={this.handleClickNext}
        customWidgetMap={customWidgetMap}
        validationErrors={validationErrors}
        onBlockErrors={this.handleShowErrors}
        interpreter={interpreter}
        versionInfo={versionInfo}
        onlyShow={onlyShow}
        onlyShowNextLabel={onlyShowNextLabel}
        showCalculateError={showCalculateError}
        scriptName={scriptName}
        SectionComponent={SectionComponent}
      />
    );
  }
}

export const mapStateToProps = (
  store: PlayerStoreType,
  { scriptName }: IPlayerProps,
): IStateToProps => {
  const scriptData = store?.[scriptName] as IScriptPlayerStore;

  if (!scriptData) {
    return {
      scriptStore: null,
      playerError: null,
      startScreenTime: null,
      versionInfo: null,
      customWidgetMap: null,
    };
  }

  const { script: scriptStore, error, versionInfo, customWidgetMap } = scriptData;

  return {
    scriptStore,
    playerError: error,
    startScreenTime: scriptStore.startScreenTime || null,
    versionInfo,
    customWidgetMap,
  };
};

export const mapDispatchToProps = (
  dispatch: ThunkDispatch<PlayerStoreType, any, PlayerActionsTypes>,
  { outsideContext, script, scriptName, onChangeHistory, marketplaces }: IPlayerProps,
): IPropsDispatch => ({
  clearScriptDataAction: () => dispatch(PlayerActions.clearScriptDataAction(scriptName)),
  getAndStartScript: () => {
    dispatch(restartAndRunScriptAction({ script, scriptName, startContext: outsideContext }));
  },
  updateContextDiffAction: (diff: IPlayerContextDiff, shouldUpdateInterpreter: boolean = true) =>
    dispatch(PlayerActions.updateContextDiffAction(scriptName, diff, shouldUpdateInterpreter)),
  goBackAction: () => dispatch(PlayerActions.goBackAction(scriptName)),
  goNextAction: () => dispatch(goNextAction(scriptName)),
  showPlayerError: (scriptPlayerError: IError) =>
    dispatch(PlayerActions.playerErrorAction(scriptName, scriptPlayerError)),
  showBlockErrors: (scriptBlockErrors: IError[]) =>
    dispatch(PlayerActions.blockErrorsAction(scriptName, scriptBlockErrors)),
  showCalculateError: (scriptCalculateError: IError) =>
    dispatch(PlayerActions.calculateErrorAction(scriptName, scriptCalculateError)),
  addBMData: (businessMonitoring: IBusinessMonitoring) =>
    dispatch(
      PlayerActions.addBusinessMonitoringDataToHistoryAction(scriptName, businessMonitoring),
    ),
  onSwitchToBlock: (blockId: string, saveContext?: boolean) =>
    dispatch(switchToBlock(scriptName, blockId, saveContext)),
  getVersionAction: () => dispatch(PlayerActions.getVersionAction(scriptName)),
  saveScriptHistoryAction: onChangeHistory
    ? debounce(() => dispatch(saveScriptHistoryAction(scriptName, onChangeHistory)), TIME_DEBOUNCE)
    : null,

  fetchMarketPlaceListAction: (marketPlaceList: IMarketPlace[]) =>
    dispatch(fetchMarketPlaceListAction(scriptName, marketPlaceList)),
  validateMarketPlaceWidgetsAction: () =>
    dispatch(validateMarketPlaceWidgetsAction(scriptName, marketplaces)),
});

export default withStore(
  connect<IStateToProps, IPropsDispatch, IPlayerProps>(mapStateToProps, mapDispatchToProps)(Player),
);
