const addBusinessMonitoringDataToHistory = (
  store: PlayerStoreType,
  {
    scriptName,
    businessMonitoring,
  }: GetPlayerActionType<'addBusinessMonitoringDataToHistoryAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  const businessMonitoringHistory = script.businessMonitoringHistory.concat(businessMonitoring);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        businessMonitoringHistory,
      },
    },
  };
};

const onStartShowScreen = (
  store: PlayerStoreType,
  { scriptName, startScreenTime }: GetPlayerActionType<'onStartShowScreenAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];

  const {
    currentBlockId,
    script: { blocks },
    playerContext,
    interpreter,
  } = script;

  const screenBlock = getBlockById(currentBlockId, blocks) as IBlockScreen;
  const initializedContext = cloneContext(playerContext);
  const blockErrors: IScriptBlockError[] = [];
  const scriptCalculateErrors: IScriptCalculateError[] = [];

  screenBlock &&
    screenBlock.widgets.forEach((widget: IWidget<ISelectBlockProps<ContextPath>>) => {
      const {
        props: { options, isSelectedDefault },
        component,
        contextName,
      } = widget;

      // Проверка для виджета selectBlock
      if (
        component === EDefaultWidget.SELECT_BLOCK &&
        isSelectedDefault &&
        options &&
        get(initializedContext, contextName) === null
      ) {
        let newValue;
        if (options.isContextPath) {
          try {
            newValue = options.getValue(initializedContext)[0].value;
          } catch (error) {
            blockErrors.push(
              prepareBlockErrorExpected({
                title: ERROR_TITLE_CONTEXT_PATH,
                text: error.message,
                blockId: currentBlockId,
                additionalId: 'options',
              }),
            );
          }
        } else {
          newValue = options[0].value;
        }
        set(initializedContext, contextName, newValue);

        // Обновляем контекст в интерпретаторе.
        try {
          interpreter.updateContextVariable(contextName, newValue);
        } catch (error) {
          scriptCalculateErrors.push(
            prepareCalculateErrorExpected({
              title: 'Ошибка при обновлении контекста для интерпретатора.',
              message: error.message,
              stack: error.stack,
              blockId: currentBlockId,
              additionalId: contextName,
            }),
          );
        }
      }
    });

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        playerContext: initializedContext,
        startScreenTime,
        status: blockErrors.length > 0 ? EStatus.ERROR : script.status,
        scriptBlockErrors: concatUniqArrayErrors(script.scriptBlockErrors, blockErrors),
        scriptCalculateErrors: concatUniqArrayErrors(
          script.scriptCalculateErrors,
          scriptCalculateErrors,
        ),
      },
    },
  };
};

const addScriptData = (
  store: PlayerStoreType,
  { scriptName, scriptData, playerContext }: GetPlayerActionType<'addScriptDataAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];

  const interpreter = new Interpreter(playerContext, script.script.macros);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      error: null,
      status: EStatus.SUCCESS,
      script: {
        ...store[scriptName].script,
        playerContext,
        interpreter,
        status: EStatus.SUCCESS,
        error: null,
        script: scriptData,
      },
    },
  };
};

const restorePlayerHistory = (
  store: PlayerStoreType,
  {
    scriptName,
    history,
    playerContext,
    currentBlockId,
  }: GetPlayerActionType<'restorePlayerHistoryAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  const newInterpreter = new Interpreter(playerContext, script.script.macros);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...store[scriptName].script,
        currentBlockId,
        history,
        playerContext,
        interpreter: newInterpreter,
        ...prepareVisibleBlock(script, currentBlockId, newInterpreter),
      },
    },
  };
};

const updateContextDiff = (
  store: PlayerStoreType,
  {
    scriptName,
    diff,
    shouldUpdateInterpreter,
  }: GetPlayerActionType<'updateContextDiffAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  const {
    script: { macros },
    currentBlockId,
    playerContext,
    interpreter,
  } = script;

  // клонируем контекст
  const newPlayerContext = { ...playerContext };

  // сливаем изменения.
  Object.keys(diff).forEach(key => {
    set(newPlayerContext, key, diff[key]);
  });

  let newInterpreter = interpreter;

  if (shouldUpdateInterpreter) {
    // Обновляем интерпретатор.
    newInterpreter = new Interpreter(newPlayerContext, macros);
  }

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...store[scriptName].script,
        ...prepareVisibleBlock(script, currentBlockId, interpreter),
        playerContext: newPlayerContext,
        interpreter: newInterpreter,
      },
    },
  };
};

const resetScreenContext = (
  store: PlayerStoreType,
  { scriptName }: GetPlayerActionType<'resetScreenContextAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  const initialBlockContext = script.initialBlockContext;
  const interpreter = new Interpreter(initialBlockContext, script.script.macros);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        playerContext: initialBlockContext,
        interpreter,
      },
    },
  };
};

const goBack = (
  store: PlayerStoreType,
  { scriptName }: GetPlayerActionType<'goBackAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  let newHistory: IHistoryContext[] = [...script.history];
  let oldPlayerContext: IPlayerContext = { ...script.playerContext };
  let oldScreenId: string = script.currentBlockId;
  let oldInterpreter: Interpreter = script.interpreter;
  let oldBlockType: TEBlockType;
  let lastHistory: IHistoryContext;

  // Отматываем историю назад, до первого блока экрана или пока не окончатся блоки в истории.
  do {
    lastHistory = newHistory.pop();
    if (lastHistory) {
      oldPlayerContext = lastHistory.playerContext;
      oldScreenId = lastHistory.blockId;
      oldBlockType = lastHistory.blockType;
      oldInterpreter = new Interpreter(oldPlayerContext, script.script.macros);
    }
  } while (lastHistory && oldBlockType !== EBlockType.SCREEN);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        ...prepareVisibleBlock(script, oldScreenId, oldInterpreter),
        history: newHistory,
        playerContext: oldPlayerContext,
        currentBlockId: oldScreenId,
        interpreter: oldInterpreter,
      },
    },
  };
};

/**
 * Переключаем блок в store.
 * Если уходим с экрана, то так же сохраняем контекст в историю.
 */
const innerSwitchToBlock = (
  store: PlayerStoreType,
  { scriptName, blockId: newBlockId }: GetPlayerActionType<'innerSwitchToBlockAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  const oldBlockId = script.currentBlockId;
  const oldBlock = getBlockById(oldBlockId, script.script.blocks);
  const shouldSaveHistory = oldBlock && oldBlock.type;
  const contextCopy = cloneContext(script.playerContext);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        currentBlockId: newBlockId,
        initialBlockContext: contextCopy,
        ...prepareVisibleBlock(script, newBlockId, script.interpreter),
        history: shouldSaveHistory
          ? script.history.concat({
              blockId: oldBlockId,
              blockType: oldBlock.type,
              playerContext: contextCopy,
            })
          : script.history,
      },
    },
  };
};

const resetScript = (
  store: PlayerStoreType,
  { scriptName, playerContext }: GetPlayerActionType<'resetScriptAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];
  const interpreter = new Interpreter(playerContext, script.script.macros);

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        currentBlockId: null,
        history: [],
        playerContext,
        interpreter,
      },
    },
  };
};

