const clearErrorPlayerScript = (
  store: PlayerStoreType,
  { scriptName, errorIndex }: GetPlayerActionType<'clearErrorPlayerScriptAction'>['payload'],
): PlayerStoreType => {
  const { script } = store[scriptName];

  const newScriptBlockErrors = script.scriptBlockErrors.filter(
    (errorUploadScript, index) => errorIndex !== index,
  );

  return {
    ...store,
    [scriptName]: {
      ...store[scriptName],
      script: {
        ...script,
        status: EStatus.ERROR,
        scriptBlockErrors: newScriptBlockErrors,
      },
    },
  };
};

