/**
 * Редьюсер для компонента проигрывателя скриптов и переключателя фаз скрипта.
 */
export default function scriptPlayerReducer(
  store: PlayerStoreType = initialPlayerStore(),
  action: PlayerActionsTypes,
): PlayerStoreType {
  switch (action.type) {
    case PlayerActionTypes.PLAYER_CLEAR_SCRIPT_DATA:
      return clearScriptData(store, action.payload);

    case PlayerActionTypes.PLAYER_START_SCRIPT_LOAD:
      return startScriptLoad(store, action.payload);

    case PlayerActionTypes.PLAYER_GET_SCRIPT_DATA_ERROR_CLEAR:
      return clearErrorGetScriptData(store, action.payload);

    case PlayerActionTypes.PLAYER_CLEAR_MONITOR_HISTORY:
      return clearBusinessMonitoringHistory(store, action.payload);

    case PlayerActionTypes.PLAYER_ADD_MONITOR_TO_HISTORY:
      return addBusinessMonitoringDataToHistory(store, action.payload);

    case PlayerActionTypes.PLAYER_ON_START_SHOW_SCREEN:
      return onStartShowScreen(store, action.payload);

    case PlayerActionTypes.PLAYER_GET_SCRIPT_DATA_SUCCESS:
      return addScriptData(store, action.payload);

    case PlayerActionTypes.PLAYER_RESTORE_SCRIPT_HISTORY:
      return restorePlayerHistory(store, action.payload);

    case PlayerActionTypes.PLAYER_UPDATE_CONTEXT_DIFF:
      return updateContextDiff(store, action.payload);

    case PlayerActionTypes.RESET_SCREEN_CONTEXT:
      return resetScreenContext(store, action.payload);

    case PlayerActionTypes.PLAYER_GO_BACK:
      return goBack(store, action.payload);

    case PlayerActionTypes.PLAYER_FOCUS_BLOCK:
      return innerSwitchToBlock(store, action.payload);

    case PlayerActionTypes.PLAYER_RESTART:
      return resetScript(store, action.payload);

    case PlayerActionTypes.PLAYER_SCRIPT_ERROR:
      return blockErrors(store, action.payload);

    case PlayerActionTypes.PLAYER_SCRIPT_ERROR_CLEAR:
      return clearErrorPlayerScript(store, action.payload);

    case PlayerActionTypes.PLAYER_CALCULATE_ERROR:
      return calculateError(store, action.payload);

    case PlayerActionTypes.PLAYER_CALCULATE_ERROR_CLEAR:
      return clearErrorPlayerCalculate(store, action.payload);

    case PlayerActionTypes.PLAYER_ERROR:
      return playerError(store, action.payload);

    case PlayerActionTypes.PLAYER_ERROR_CLEAR:
      return clearErrorPlayer(store, action.payload);

    case PlayerActionTypes.PLAYER_SET_CUSTOM_WIDGET_MAP:
      return setCustomWidgetMap(store, action.payload);

    default:
      return store;
  }
}
