import InfoCircleOutlined from '@ant-design/icons/InfoCircleOutlined';
import React from 'react';

import { TooltipProps } from 'antd';

import { MarkdownReader } from '_components/MardownReader';

/**
 * Функция возвращает данные для формирования тултипа для Form.Item
 *
 * @param {string} hint - содержимое подсказки
 */
export const getFormItemHint = (hint?: string): TooltipProps & { icon: React.ReactNode } => {
  if (!hint) return null;

  return {
    title: <MarkdownReader content={hint} />,
    icon: <InfoCircleOutlined />,
    overlayClassName: 'ssp-player-form-item-hint',
  };
};
