import thunk from 'redux-thunk';

import { createStore, applyMiddleware, compose, StoreEnhancer, Reducer, Middleware } from 'redux';

import playerReducer from './reducers/PlayerReducer';

const composeEnhancers =
  NODE_ENV === 'development' && window['__REDUX_DEVTOOLS_EXTENSION_COMPOSE__']
    ? (window['__REDUX_DEVTOOLS_EXTENSION_COMPOSE__'] as typeof compose)
    : compose;

const middleWares: Middleware[] = [thunk];

if (NODE_ENV === 'development') {
  const { createLogger } = require(`redux-logger`);

  const logger = createLogger({
    collapsed: (getState, action, logEntry) => !logEntry.error,
  });

  middleWares.push(logger);
}

const enhancer: StoreEnhancer = composeEnhancers(applyMiddleware(...middleWares));

export default createStore(playerReducer as Reducer, enhancer);
