import * as React from 'react';

import { Form, InputNumber } from 'antd';

import isBoolean from 'lodash/isBoolean';
import isEqual from 'lodash/isEqual';
import isNumber from 'lodash/isNumber';
import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IErrorBase, IWidgetBaseProps, WidgetClassStateless } from '_app/Models';
import { canBeEmptyProp, checkIsBigDecimal } from '_app/utils';
import { CopyButtonComp } from '_components/CopyButtonComp';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { getWidgetContextError, getWidgetPropertyError } from './widgetUtils';

/**
 * Свойства поля ввода числового значения.
 *
 * @prop {string} [label] Подпись над полем ввода.
 * @prop {string} [currency] Валюта ввода.
 * @prop {number} [fractionalDigitsCount] Количество знаков после запятой.
 * @prop {number} [disabled] Возможно ли редактирование.
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps extends IWidgetBaseProps<number> {
  label?: string;
  currency?: string;
  fractionalDigitsCount?: number;
  disabled?: boolean;
  readOnly?: boolean;
  layout?: EFormLayout;
}

/**
 * Поле ввода числового значения.
 */
export class NumberInputBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Ввод чисел';

  static validator(props: IProps): IErrorBase[] {
    const { value, label, currency, fractionalDigitsCount, disabled, layout } = props;
    let errors: IErrorBase[] = [];

    /**
     * value должен преобразовываться в число
     * все числовые значения, сохраненные и извлеченные из localStorage, будут строками
     */
    if (value !== null && !(!isNaN(Number(value)) || checkIsBigDecimal(value))) {
      errors.push(getWidgetContextError(this.widgetName, [EPropRsType.NUMBER], value));
    }

    /**
     * label должен быть строкой.
     */
    if (canBeEmptyProp(label) && !isString(label)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'label', [EPropRsType.STRING], label),
      );
    }

    /**
     * currency должен быть строкой.
     */
    if (canBeEmptyProp(currency) && !isString(currency)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'currency', [EPropRsType.STRING], currency),
      );
    }

    /**
     * fractionalDigitsCount должен быть числом.
     */
    if (
      canBeEmptyProp(fractionalDigitsCount) &&
      !(isNumber(fractionalDigitsCount) || checkIsBigDecimal(fractionalDigitsCount))
    ) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'fractionalDigitsCount',
          [EPropRsType.NUMBER],
          fractionalDigitsCount,
        ),
      );
    }

    /**
     * disabled должен быть логическим типом.
     */
    if (canBeEmptyProp(disabled) && !isBoolean(disabled)) {
      errors.push(
        getWidgetPropertyError<IProps>(
          this.widgetName,
          'disabled',
          [EPropRsType.BOOLEAN],
          disabled,
        ),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps) {
    return (
      !isEqual(this.props.value, nextProps.value) ||
      this.props.label !== nextProps.label ||
      this.props.currency !== nextProps.currency ||
      this.props.disabled !== nextProps.disabled ||
      this.props.fractionalDigitsCount !== nextProps.fractionalDigitsCount ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.layout !== nextProps.layout
    );
  }

  handleChange = (value: string | number) => {
    if (isNumber(value)) {
      this.props.onChange(value);
    } else {
      this.props.onChange(null);
    }
  };

  render() {
    return (
      <FormWidgetContainer layout={this.props.layout}>
        <Form.Item
          data-test-id="NumberInputBlock.Component"
          required={this.props.onlyShow === true ? false : this.props.isRequired}
          label={this.props.label ?? ' '}
          colon={false}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          extra={this.props.currency}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <InputNumber
            data-test-id="NumberInputBlock.input"
            defaultValue={this.props.value}
            prefix={
              this.props.onlyShow === true && (
                <CopyButtonComp data-test-id="TextInputBlock.copy" value={`${this.props.value}`} />
              )
            }
            value={this.props.value}
            precision={this.props.fractionalDigitsCount}
            onChange={this.handleChange}
            onBlur={this.props.onBlur}
            disabled={this.props.onlyShow || this.props.disabled || this.props.readOnly}
          />
        </Form.Item>
      </FormWidgetContainer>
    );
  }
}
