import * as React from 'react';

import { Collapse } from 'antd';

import isArray from 'lodash/isArray';

import { EPropRsType } from '_app/Enums';
import { IOption, WidgetClass, IWidgetBaseProps, IErrorBase } from '_app/Models';
import { MarkdownReader } from '_components/MardownReader';

import { NOT_FOUND_ACCORDIONS } from '../Const';
import { getWidgetPropertyError, validateDefaultOptions } from './widgetUtils';

const { Panel } = Collapse;

/**
 * Свойства мини-аккордеона.
 *
 * @prop {IOption} options Список аккордеонов.
 */
export interface IProps extends IWidgetBaseProps<void> {
  options: IOption<string>[];
}

/**
 * Состояние мини-аккордеона.
 *
 * @prop {number} opened Номер выбранного аккордеона.
 */
export interface IState {
  opened: number;
}

/**
 * Мини-аккордеон.
 */
export class AccordionBlock extends WidgetClass<IProps, IState> {
  static widgetName = 'Аккордеон';

  state: IState = {
    opened: 0,
  };

  static validator(props: IProps): IErrorBase[] {
    const { options } = props;
    let errors: IErrorBase[] = [];

    /**
     * Значение options должно быть массивом объектов, состоящим из двух текстовых полей label и value.
     */
    if (isArray(options)) {
      errors.push(...validateDefaultOptions(options, this.widgetName));
    } else {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'options', [EPropRsType.ARRAY], options),
      );
    }

    return errors;
  }

  shouldComponentUpdate(nextProps: IProps, nextState: IState) {
    return this.props.options !== nextProps.options || this.state.opened !== nextState.opened;
  }

  render() {
    const { options } = this.props;
    const { opened } = this.state;

    return isArray(options) && options.length > 0 ? (
      <Collapse data-test-id="AccordionBlock.Collapse" accordion defaultActiveKey={opened}>
        {options.map((item: IOption<string>, index) => (
          <Panel data-test-id="AccordionBlock.Panel" header={item.label} key={index}>
            <MarkdownReader content={item.value} />
          </Panel>
        ))}
      </Collapse>
    ) : (
      <p data-test-id="AccordionBlock.notFound">{NOT_FOUND_ACCORDIONS}</p>
    );
  }
}
