export const fetchApi = async (url: string, options = {}, withHeaders = false) => {
  try {
    // @ts-ignore
    options?.mode = 'cors';
    const response = await fetch(url, options);

    if (response.status === 204 || response.status === 500) {
      return {
        ok: response.ok,
        status: response.status,
      };
    }

    const json = await response.json();
    if (response.ok) {
      if (withHeaders) {
        return { json };
      }
      return json;
    }

    throw {
      internalError: json,
      errorStatus: response.status,
    };
  } catch (error) {
    console.error(`API ${url}`, error);

    throw error;
  }
};
