import isString from 'lodash/isString';
import isUndefined from 'lodash/isUndefined';
import isNaN from 'lodash/isNaN';
import isNumber from 'lodash/isNumber';
import isNull from 'lodash/isNull';

import { EPropRsType } from '_app/Enums';
import { IErrorBase, IOption } from '_app/Models';
import { getClass, checkIsBigDecimal } from '_app/utils';

/**
 * Переводы типов данных
 */
export const typesTranslate = {
  [EPropRsType.OBJECT]: 'в формате объекта',
  [EPropRsType.ARRAY]: 'в формате массива',
  [EPropRsType.STRING]: 'в формате строки',
  [EPropRsType.NUMBER]: 'в формате числа',
  [EPropRsType.BOOLEAN]: 'в логическом формате',
  [EPropRsType.TEMPLATE]: 'в формате шаблонной строки',
  [EPropRsType.LINK]: 'в формате ссылки',
  [EPropRsType.DATE]: 'в формате даты',
  [EPropRsType.EXPRESSION]: 'в формате выражения',
  NaN: 'NaN',
  null: 'null',
  undefined: 'undefined',
};

/**
 * Получаем перевод типа данных.
 *
 * @param {string | EPropRsType} typeContext Тип данных.
 */
export function getTypeTranslate(typeContext: string | EPropRsType) {
  return typesTranslate[typeContext] ? typesTranslate[typeContext] : 'неизвестное';
}

/**
 * Получить ошибку для контекстной переменной.
 *
 * @param {string} widgetName Название виджета.
 * @param {EPropRsType[]} typeContexts Тип данных.
 * @param {any} value Значение.
 * @param {number} valueIndex Индекс значения.
 */
export function getWidgetContextError(
  widgetName: string,
  typeContexts: EPropRsType[],
  value: any,
  valueIndex?: number,
): IErrorBase {
  const expect = typeContexts.map(getTypeTranslate).join(', ');
  const given = getTypeTranslate(getClass(value));

  return {
    title: `В виджет "${widgetName}" передано некорректное значение для изменения переменной контекста.`,
    text:
      `В виджет "${widgetName}" передано для изменения контекста некорректное значение` +
      `${
        !isUndefined(valueIndex) ? ` по индексу "${valueIndex}"` : ''
      }. Ожидается: ${expect}. Передано: ${given}.`,
  };
}

/**
 * Получить ошибку для примитивного свойства виджета.
 *
 * @param {string} widgetName Название виджета.
 * @param {keyof T} propName Название свойства виджета.
 * @param {EPropRsType[]} typeContexts Тип данных.
 * @param {Partial<T>} prop Значение свойства виджета.
 */
export function getWidgetPropertyError<T>(
  widgetName: string,
  propName: keyof T,
  typeContexts: EPropRsType[],
  prop: any,
): IErrorBase {
  const expect = typeContexts.map(getTypeTranslate).join(', ');
  const given = getTypeTranslate(getClass(prop));

  return {
    title: `В виджет "${widgetName}" передано некорректное значение для свойства "${propName}".`,
    text: `В виджете "${widgetName}" для свойства "${propName}" передано некорректное значение. Ожидается: ${expect}. Передано: ${given}.`,
  };
}

/**
 * Получить ошибку для option свойства виджета.
 *
 * @param {string} widgetName Название виджета.
 * @param {keyof T} propName Название свойства с массивом элементов.
 * @param {keyof S} optionName Название свойства в options.
 * @param {EPropRsType[]} typeContexts Тип данных.
 * @param {Partial<T>} option Значение параметра в options.
 * @param {number} optionIndex Индекс параметра в options.
 */
export function getWidgetOptionError<T, S>(widgetName: string, propName: keyof T) {
  return (
    optionName: keyof S,
    typeContexts: EPropRsType[],
    option: any, //Partial<T>,
    optionIndex: number,
  ): IErrorBase => {
    const expect = typeContexts.map(getTypeTranslate).join(', ');
    const given = getTypeTranslate(getClass(option));
    const title = `В виджет "${widgetName}" передано некорректное значение для свойства массива "${propName}".`;

    const pathOption = `${
      optionName ? ` для параметра "${optionName}"` : ''
    } по индексу "${optionIndex}" массива "${propName}"`;

    if (typeContexts.length > 0) {
      return {
        title,
        text: `В виджете "${widgetName}" ${pathOption} передано некорректное значение. Ожидается: ${expect}. Передано: ${given}.`,
      };
    } else {
      return {
        title,
        text: `В виджете "${widgetName}" ${pathOption} не передано значение.`,
      };
    }
  };
}

/**
 * Проверка что параметр является числом или строкой.
 *
 * @param {any} value Значение для проверки.
 */
export function propIsStringOrNumber(value: any) {
  return !(isString(value) || (isNumber(value) && !isNaN(value)) || checkIsBigDecimal(value));
}

/**
 * Проверка значений для options, значения которого состоят из label и value.
 *
 * @param {IOption<string>[]} options Массив для проверки.
 * @param {string} widgetName Название виджета.
 */
export function validateDefaultOptions(
  options: IOption<string>[],
  widgetName: string,
): IErrorBase[] {
  let errors: IErrorBase[] = [];
  const getError = getWidgetOptionError<any, IOption<string>>(widgetName, 'options');

  // Что бы не вываливать весь список ошибок, если передан массив с другими объектами, показываем только первую встречную ошибку.
  options.some(({ label, value, select }: IOption<string>, optionIndex: number) => {
    let hasError = false;

    if (!isString(label)) {
      hasError = true;
      errors.push(getError('label', [EPropRsType.STRING], label, optionIndex));
    }

    if (propIsStringOrNumber(value)) {
      hasError = true;
      errors.push(getError('value', [EPropRsType.STRING, EPropRsType.NUMBER], value, optionIndex));
    }

    if (select && !isString(select)) {
      hasError = true;
      errors.push(getError('select', [EPropRsType.STRING], select, optionIndex));
    }

    return hasError;
  });

  return errors;
}

/**
 * Получить массив ключей из enum.
 *
 * @param {any} enumValue enum, из которого получаем ключи.
 */
export function getEnumKeys(enumValue: any): string[] {
  return Object.keys(enumValue).filter(key => isNaN(+key));
}

/**
 * Получить ошибку, если значение не соответствует enum.
 *
 * @param {string[]} availableDateTypes Массив из enum ключей.
 * @param {string} value Значение для проверки.
 * @param {string} widgetName Название виджета.
 * @param {keyof T} propName Название свойства виджета.
 */
export function getEnumError<T>(
  availableDateTypes: string[],
  value: string,
  widgetName: string,
  propName: keyof T,
) {
  const expect = availableDateTypes.map(availableDateType => `"${availableDateType}"`).join(', ');

  return {
    title: `В виджет "${widgetName}" передано некорректное значение для свойства "${propName}".`,
    text: `В виджете "${widgetName}" для свойства "${propName}" передано некорректное значение. Ожидается: ${expect}. Передано: "${value}".`,
  };
}

/**
 * Получить ошибку, если значение не соответствует enum.
 *
 * @param {string} widgetName Название виджета.
 * @param {keyof T} propName Название свойства виджета.
 * @param {string[]} availableDateTypes Массив из enum ключей.
 * @param {string} value Значение для проверки.
 * @param {string} [optionName] Название параметра массива.
 * @param {number} [optionIndex] Индекс опции.
 */
export function getEnumErrorInOptions<T, S>(widgetName: string, propName: keyof T) {
  return function (
    availableDateTypes: string[],
    value: string,
    optionName: keyof S,
    optionIndex: number,
  ) {
    const expect = availableDateTypes.map(availableDateType => `"${availableDateType}"`).join(', ');
    const pathOption = `для параметра "${optionName}" по индексу "${optionIndex}" массива "${propName}"`;

    return {
      title: `В виджет "${widgetName}" ${pathOption} передано некорректное значение.`,
      text: `В виджете "${widgetName}" ${pathOption}  передано некорректное значение. Ожидается: ${expect}. Передано: "${value}".`,
    };
  };
}

/**
 * Заменяем все < и > на &lt; и &gt;.
 *
 * @param {string} content Содержимое Markdown-а.
 */
export function replaceTagsForMarkdown(content: any): string {
  return !isNull(content)
    ? content.toString().replace(new RegExp('<', 'g'), '&lt;').replace(new RegExp('>', 'g'), '&gt;')
    : '';
}
