import React from 'react';

import { IWidgetBaseProps, WidgetClassStateless } from '_app/Models';

import { TextInputBlock } from './TextInputBlock';

/**
 * Свойства компонента.
 *
 * @prop {string} [label] Подпись над полем ввода.
 * @prop {string} [placeholder] Текст, выводимый в поле ввода, если значение не задано
 */
export interface IProps extends IWidgetBaseProps<string> {
  label?: string;
  placeholder?: string;
}

/**
 * Поле ввода электронной почты.
 */
export class EmailInputBlock extends WidgetClassStateless<IProps> {
  static widgetName = 'Ввод электронной почты';

  render() {
    return <TextInputBlock data-test-id="EmailInputBlock.Component" {...this.props} />;
  }
}
