import * as React from 'react';

import { Form, Radio } from 'antd';

import { RadioChangeEvent } from 'antd/es';

import isArray from 'lodash/isArray';
import isString from 'lodash/isString';

import { EFormLayout, EPropRsType } from '_app/Enums';
import { IOption, IWidgetBaseProps, WidgetClass, IErrorBase } from '_app/Models';
import { canBeEmptyProp } from '_app/utils';
import { getFormItemHint } from '_components/FormItemHint';
import { FormWidgetContainer } from '_components/FormWidgetContainer';

import { NOT_FOUND_RADIO_BUTTONS } from '../Const';
import {
  getWidgetContextError,
  getWidgetPropertyError,
  validateDefaultOptions,
  propIsStringOrNumber,
} from './widgetUtils';

/**
 * Свойства компонента.
 *
 * @prop {string} [label] Подпись над полем ввода.
 * @prop {IOption[]} options Список вариантов выбора.
 * @prop {boolean} [readOnly] Флаг отвечающий за блокировку виджета
 * @prop {EFormLayout} layout Layout для виджета.
 */
export interface IProps extends IWidgetBaseProps<string> {
  label?: string;
  options: IOption<string>[];
  readOnly?: boolean;
  layout?: EFormLayout;
}

/**
 * Состояние компонента.
 *
 * @prop {string} value Текущий выбранный пункт.
 */
export interface IState {
  value?: string;
}

/**
 * Обёртка над RadioGroup.
 */
export class RadioGroupBlock extends WidgetClass<IProps, IState> {
  static widgetName = 'Переключатель';

  state: IState = {
    value: this.props.value ? this.props.value : null,
  };

  static validator(props: IProps): IErrorBase[] {
    const { value: contextValue, options, layout } = props;
    let errors: IErrorBase[] = [];

    /**
     * Значение должно быть строкой или числом.
     */
    if (contextValue !== null && propIsStringOrNumber(contextValue)) {
      errors.push(
        getWidgetContextError(
          this.widgetName,
          [EPropRsType.STRING, EPropRsType.NUMBER],
          contextValue,
        ),
      );
    }

    /**
     * Значение options должно быть массивом объектов, состоящим из двух текстовых полей label и value.
     */
    if (isArray(options)) {
      errors.push(...validateDefaultOptions(options, this.widgetName));
    } else {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'options', [EPropRsType.ARRAY], options),
      );
    }

    /**
     * layout должен быть строкой.
     */
    if (canBeEmptyProp(layout) && !isString(layout)) {
      errors.push(
        getWidgetPropertyError<IProps>(this.widgetName, 'layout', [EPropRsType.STRING], layout),
      );
    }

    return errors;
  }

  componentDidUpdate(prevProps: IProps) {
    if (this.props.onlyShow === true) {
      return;
    }
    // Проверка что пришло новое значение.
    if (prevProps.value !== this.props.value) {
      this.setState({
        value: this.props.value,
      });
    }
  }

  shouldComponentUpdate(nextProps: IProps, nextState: IState) {
    return (
      this.props.value !== nextProps.value ||
      this.props.options !== nextProps.options ||
      this.state.value !== nextState.value ||
      this.props.hasError !== nextProps.hasError ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.errorMessage !== nextProps.errorMessage ||
      this.props.hint !== nextProps.hint ||
      this.props.readOnly !== nextProps.readOnly ||
      this.props.label !== nextProps.label
    );
  }

  /**
   * Переключение радиобаттона при клике.
   *
   * @param {RadioChangeEvent} event Событие изменения радиобаттона.
   */
  onChange = (event: RadioChangeEvent) => {
    if (this.props.onlyShow === true) {
      return;
    }
    const value = event.target.value;
    this.setState({ value }, () => this.props.onChange(value));
  };

  render() {
    const { options, hasError, layout } = this.props;

    return options.length > 0 ? (
      <FormWidgetContainer layout={layout}>
        <Form.Item
          data-test-id="RadioGroupBlock.Component"
          required={this.props.onlyShow === true ? false : this.props.isRequired}
          label={this.props.label ?? ' '}
          colon={false}
          validateStatus={this.props.hasError ? 'error' : null}
          help={this.props.errorMessage ? this.props.errorMessage : null}
          tooltip={getFormItemHint(this.props.hint)}
        >
          <Radio.Group
            data-test-id="RadioGroupBlock.radio"
            defaultValue={this.state.value}
            value={this.state.value}
            onChange={this.onChange}
            disabled={this.props.onlyShow || this.props.readOnly}
          >
            {options.map((item: IOption<string>) => (
              <Radio data-test-id="RadioGroupBlock.radio.item" key={item.value} value={item.value}>
                {item.label}
              </Radio>
            ))}
          </Radio.Group>
        </Form.Item>
      </FormWidgetContainer>
    ) : (
      <p
        data-test-id="RadioGroupBlock.noItem"
        className={hasError ? 'ssp-player-validation-error' : null}
      >
        {NOT_FOUND_RADIO_BUTTONS}
      </p>
    );
  }
}
