import React, { Component, SyntheticEvent, JSXElementConstructor } from 'react';

import { Button, Col, Collapse, Row, Space } from 'antd';
import cx from 'classnames';

import cloneDeep from 'lodash/cloneDeep';
import forEach from 'lodash/forEach';
import get from 'lodash/get';
import isArray from 'lodash/isArray';
import isEmpty from 'lodash/isEmpty';
import isEqual from 'lodash/isEqual';
import isObject from 'lodash/isObject';

import {
  IBackendError,
  IBlockScreen,
  IButtonRouteProps,
  ICustomWidget,
  ICustomWidgetData,
  IError,
  IErrorBase,
  IInterpreter,
  IPlayerContext,
  IScreenMenuProps,
  ISectionComponentProps,
  IValidationError,
  IVersionInfo,
  IWidget,
  IWidgetClass,
  IWidgetDefinition,
  IWidgetProps,
} from '_app/Models';
import {
  prepareBlockErrorExpected,
  prepareCalculateErrorExpected,
  prepareWidgetErrorExpected,
} from '_app/errors';
import { getSectionParam } from '_app/mapper';
import { executeAsync, isKnownWidget } from '_app/utils';
import { CustomWidget } from '_components/widget/CustomWidget/CustomWidget';

// Стили для виджетов проигрывателя.
import '../../styles/index.less';
import { EComponentType, EDefaultWidget } from '../Enums';
import { ContextPath } from '../WidgetPropTypes/ContextPath';
import { TemplatedString } from '../WidgetPropTypes/TemplatedString';
import {
  ERROR_TITLE_CONTEXT_PATH,
  VALIDATION_ERROR_MARKER_CLASS,
  VALIDATION_SOURCE_NEXT,
  WIDGET_MAP,
} from './Const';

import { WidgetWrapper } from '_components/WidgetWrapper';

/**
 * @prop {IBlockScreen} block Блок экрана.
 * @prop {IPlayerContext} playerContext Контекст плеера.
 * @prop {boolean} hasHistory Признак наличия истории.
 * @prop {Function} onChangeWidget Динамический пересчёт экрана.
 * @prop {Function} onBlurWidget Обработчик потери фокуса с виджета.
 * @prop {IWidgetDefinition[]} [customWidgets] Пользовательские компоненты.
 * @prop {Function} onClickPrev Переход на предыдущий экран.
 * @prop {Function} onClickNext Переход на следующий экран.
 * @prop {Record<string, ICustomWidgetData>} [customWidgetMap] Карта UI-компонентов из маркетплейсов.
 * @prop {Function} onSwitchToBlock Переход к блоку скрипта.
 * @prop {Function} onValidatedSwitchToBlock Переход к блоку скрипта с валидацией экранной формы.
 * @prop {Function} onBlockErrors Ошибки при работе блока.
 * @prop {IInterpreter} interpreter Интерпретатор выражений.
 * @prop {IValidationError[]} [validationErrors] Ошибки валидации.
 * @prop {IVersionInfo} [versionInfo] Информация по версии.
 * @prop {string} scriptName название скрипта
 * @prop {React.ReactNode} SectionComponent Внешний компонент для отрисовки блока.
 */
export interface IProps {
  block: IBlockScreen;
  playerContext: IPlayerContext;
  hasHistory: boolean;
  onChangeWidget: (contextName: string, value: any, calculatingBlockId: string) => void;
  onBlurWidget: (widget: IWidget<any>) => void;
  customWidgets?: IWidgetDefinition<any>[];
  onClickPrev: () => void;
  onClickNext: (event: SyntheticEvent | null, isNeedCustomNextAction?: boolean) => boolean;
  customWidgetMap?: Record<string, ICustomWidgetData>;
  onSwitchToBlock: (blockId: string, saveContext?: boolean) => void;
  onValidatedSwitchToBlock: (
    widget: IWidget<IButtonRouteProps>,
  ) => (blockId: string, saveContext?: boolean) => void;
  onBlockErrors: (scriptBlockErrors: IError[]) => void;
  interpreter: IInterpreter;
  validationErrors?: IValidationError[]; // TODO: validationErrors? => validationErrors
  versionInfo?: IVersionInfo;
  onlyShow?: boolean;
  onlyShowNextLabel?: string;
  showCalculateError: (scriptCalculateError: IError) => void;
  scriptName: string;
  SectionComponent?: JSXElementConstructor<ISectionComponentProps>;
}

export class Screen extends Component<IProps, {}> {
  static displayName = 'Screen';

  screenRef: HTMLDivElement;
  getScreenRef = (node: HTMLDivElement) => (this.screenRef = node);

  shouldComponentUpdate(nextProps: IProps) {
    return (
      this.props.block.id !== nextProps.block.id ||
      this.props.onlyShow !== nextProps.onlyShow ||
      this.props.onlyShowNextLabel !== nextProps.onlyShowNextLabel ||
      !isEqual(this.props.playerContext, nextProps.playerContext) ||
      this.props.validationErrors !== nextProps.validationErrors ||
      this.props.SectionComponent !== nextProps.SectionComponent
    );
  }

  componentDidUpdate(prevProps: IProps) {
    if (prevProps.block.id !== this.props.block.id) {
      this.scrollToTop();
    }
  }

  isScrollNeeded = () => {
    const scrollExists = this.screenRef?.clientHeight > window?.innerHeight;
    const scrollIsBelowScreenStart = this.screenRef?.getBoundingClientRect()?.top < 0;
    return scrollExists && scrollIsBelowScreenStart;
  };

  scrollToTop = () => {
    if (this.isScrollNeeded()) {
      this.screenRef?.scrollIntoView();
    }
  };

  /**
   * Фабрика по производству компонентов.
   *
   * @param {EDefaultWidget | string} widgetName Название компонента.
   */
  getKnownWidgetClass = (widgetName: EDefaultWidget | string): IWidgetClass<any> =>
    isKnownWidget(widgetName) ? WIDGET_MAP[widgetName] : null;

  /**
   * Парсинг ссылок на контекстные переменные в текстовых свойствах виджета.
   *
   * @param {IWidgetProps} props Исходные свойства виджета.
   */
  parseVariablesInProps = (props: IWidgetProps): IWidgetProps => {
    const { playerContext, onBlockErrors, block } = this.props;
    // делаем клон, т.к. проще всего тут рекурсивно мутировать объект в цикле
    const result = cloneDeep(props);
    forEach(result, (prop, key) => {
      if (prop && prop.isTemplatedString) {
        executeAsync(
          () => (result[key] = (prop as TemplatedString).getValue(playerContext)),
          // BPSBSCP-469 Выносим из синхронного потока, так как вызывало render несколько раз в одном процессе.
          (error: Error) =>
            onBlockErrors([
              prepareBlockErrorExpected({
                title: 'Ошибка при обработке шаблонной строки для параметров виджета.',
                text: error.message,
                blockId: block.id,
                additionalId: key,
              }),
            ]),
          () => (result[key] = ''), // Возвращаем строку, чтоб не вызывать ошибку в виджете,
          // что передан undefined вместо строки.
        );
      } else if (prop && prop.isContextPath) {
        executeAsync(
          () => (result[key] = (prop as ContextPath).getValue(playerContext)),
          // BPSBSCP-469 Выносим из синхронного потока, так как вызывало render несколько раз в одном процессе.
          (error: Error) =>
            onBlockErrors([
              prepareBlockErrorExpected({
                title: ERROR_TITLE_CONTEXT_PATH,
                text: error.message,
                blockId: block.id,
                additionalId: key,
              }),
            ]),
          () => (result[key] = undefined),
        );
      } else if (isObject(prop) || isArray(prop)) {
        result[key] = this.parseVariablesInProps(prop);
      }
    });

    return result;
  };

  /**
   * Фабрика по производству пользовательских компонентов.
   *
   * @param {EDefaultWidget | string} widgetName Название компонента.
   */
  getCustomWidgetClass = (widgetName: EDefaultWidget | string): IWidgetDefinition<any> => {
    const { customWidgets = [] } = this.props;
    const customWidget = customWidgets.find(
      widget => widgetName === (widget.name || widget.component.displayName),
    );

    return customWidget ?? null;
  };

  /**
   * Получаем для отрисовки на экране компонент и параметры для него.
   *
   * @param {IWidget<IWidgetProps>} widget Компонент для отображения.
   */
  getWidgetAndProps = (widget: ICustomWidget<IWidgetProps>) => {
    const widgetName = widget.component;
    const { customWidgetMap, showCalculateError } = this.props;

    let WidgetComponent;
    let props: IWidgetProps = widget.props ? this.parseVariablesInProps(widget.props) : {};
    // Дополнительные параметры компонента, которые не будут смержены с основными пропсами
    let additionProps: IWidgetProps = {};
    // проверяем есть ли виджет среди кастомных виджетов
    if (
      'componentType' in widget &&
      widget.componentType === EComponentType.MARKETPLACE &&
      customWidgetMap[widgetName]
    ) {
      const customWidgetData = customWidgetMap[widgetName];

      const customWidgetVersion = customWidgetData.versions.find(v => v.version === widget.version);

      if (customWidgetVersion) {
        WidgetComponent = CustomWidget;
        additionProps.url = `${customWidgetVersion.baseUrl}widget/${customWidgetData.name}/${customWidgetVersion.version}/widget.js`;
        additionProps.componentName = customWidgetData.name;

        // если у кастомного виджета есть отдельные css файлы стилей, то добавляем их в документ
        if (customWidgetData?.styles && customWidgetData?.styles?.length !== 0) {
          const head = document.querySelector('head');

          customWidgetData?.styles?.forEach(style => {
            const elementLink = document.createElement('link');
            elementLink.rel = 'stylesheet';
            elementLink.type = 'text/css';
            elementLink.href = `${customWidgetVersion.baseUrl}widget/${customWidgetData.name}/${customWidgetVersion.version}/${style}.css`;

            head.insertAdjacentElement('beforeend', elementLink);
          });
        }
      } else {
        showCalculateError(
          prepareCalculateErrorExpected({
            title: `Ошибка при загрузке кастомного виджета "${customWidgetData.name} ${customWidgetVersion.version}".`,
            message: 'Не найдено требуемой версии среди существующих в маркетплейсах',
          }),
        );
        console.error('Не найдено требуемой версии среди существующих в маркетплейсах');
      }
    } else if (WIDGET_MAP[widgetName]) {
      // проверяем есть ли виджет среди обычных виджетов
      WidgetComponent = WIDGET_MAP[widgetName];
    } else {
      // кастомный компонент
      const customWidget = this.getCustomWidgetClass(widgetName);

      if (customWidget) {
        WidgetComponent = customWidget.component;
        props = { ...customWidget.props, ...props };
      }
    }

    return { WidgetComponent, props, additionProps };
  };

  /**
   * Обработка изменения значения компонента.
   *
   * @param {IWidget} widget компонент.
   */
  handleChangeWidget = (widget: IWidget<any>) => (value: any) => {
    const {
      block: { calculatingBlockId },
      onChangeWidget,
    } = this.props;
    onChangeWidget(widget.contextName, value, calculatingBlockId);
  };

  /**
   * Обработка потери фокуса с виджета.
   *
   * @param {IWidget} widget Компонент.
   */
  handleBlurWidget = (widget: IWidget<any>) => () => {
    this.props.onBlurWidget(widget);
  };

  /**
   * Отображение стандартных кнопок Назад/Далее.
   */
  renderDefaultButtons = () => {
    const { block, onClickPrev, onClickNext, hasHistory, onlyShow, onlyShowNextLabel } = this.props;
    const { hasBack, hasNext, backLabel, nextLabel } = block;

    const onlyShowNextLabelValue =
      onlyShowNextLabel && onlyShowNextLabel.length > 0 ? onlyShowNextLabel : nextLabel;

    return (
      <Space data-test-id="Screen.navigation" style={{ marginTop: '1rem' }}>
        {hasHistory && hasBack && (
          <Button data-test-id="Screen.navigation.back" onClick={onClickPrev}>
            {backLabel}
          </Button>
        )}
        {hasNext && (
          <Button type="primary" data-test-id="Screen.navigation.next" onClick={onClickNext}>
            {onlyShow ? onlyShowNextLabelValue : nextLabel}
          </Button>
        )}
      </Space>
    );
  };

  /**
   * Отправка ошибки, которая произошла внутри виджета.
   *
   * @prop {number} row Индекс строки виджета.
   * @prop {number} column Индекс колонки виджета в строке.
   * @prop {string} contextName Переменная контекста, используемая в виджете
   */
  handleOnErrorWidget =
    (row: number, column: number, contextName: string) => (errors: IBackendError[]) => {
      this.props.onBlockErrors(
        errors.map(error =>
          prepareWidgetErrorExpected({
            title: error.title,
            text: error.text,
            widgetInfo: { row, column },
            // Добавляем информацию о виджете, в котором произошла ошибка
            additionalId: contextName,
            blockId: this.props.block.id,
          }),
        ),
      );
    };

  /**
   * Отправка ошибки, которая произошла внутри виджета.
   *
   * @prop {number} row Индекс строки виджета.
   * @prop {number} column Индекс колонки виджета в строке.
   * @prop {string} contextName Переменная контекста, используемая в виджете
   */
  handleErrorWidget = (errors: IError[]) => {
    this.props.onBlockErrors(
      errors.map((error: IError) =>
        prepareWidgetErrorExpected({
          title: error.title,
          text: error.text,
          widgetInfo: error.widgetInfo,
          // Добавляем информацию о виджете, в котором произошла ошибка
          additionalId: error.additionalId,
          blockId: this.props.block.id,
        }),
      ),
    );
  };

  /**
   * Отправка ошибки, которая произошла при обработке spruce выражения.
   *
   * @prop {expression} spruce выражение.
   * @prop {widget} IWidget  Компонент в блоке экрана.
   */
  checkInterpreterExpression = (expression, widget) => {
    const { interpreter, showCalculateError } = this.props;
    try {
      return interpreter.interpret(expression);
    } catch (error) {
      showCalculateError(
        prepareCalculateErrorExpected({
          title: `Ошибка при вычислении spruce выражения "${widget.props.label}".`,
          message: error.message,
          stack: error.stack,
        }),
      );
    }
  };

  renderGridView = () => {
    const {
      block: { widgets, sections },
      playerContext,
      validationErrors,
      SectionComponent,
      showCalculateError,
    } = this.props;

    if (!sections?.length) {
      return this.renderSectionView(widgets);
    }

    return (
      <Space direction="vertical" style={{ width: '100%' }} size="middle">
        {sections.map((section, index) => {
          const filteredWidgets = section
            ? widgets.filter(w => section.rowNumbers.includes(w.rowNumber))
            : widgets;

          if (!filteredWidgets.length) return null;

          const { name, isExpanded, isCollapsible } = section;
          let sectionName, sectionExpanded;

          try {
            sectionName = getSectionParam(name.value, name.type, playerContext);
            sectionExpanded = getSectionParam(isExpanded.value, isExpanded.type, playerContext);
          } catch (e) {
            showCalculateError(
              prepareCalculateErrorExpected({
                title: 'Ошибка при вычислении правил валидации секции.',
                message: e.message,
              }),
            );
          }

          const currentContext = filteredWidgets.map(w => w.contextName);

          const currentErrors = validationErrors
            .map(e => (typeof e.source === 'string' ? null : e.source.contextName))
            .filter(Boolean);

          const sectionHasError = currentContext.some(contextName =>
            currentErrors.includes(contextName),
          );

          // если передан внешний компонент для отрисовки блоков, возвращаем его
          if (SectionComponent) {
            return (
              <div
                key={index}
                className={sectionHasError ? VALIDATION_ERROR_MARKER_CLASS : undefined}
              >
                <SectionComponent
                  title={sectionName}
                  defaultExpanded={sectionExpanded}
                  isCollapsible={isCollapsible}
                  hasError={sectionHasError}
                >
                  {this.renderSectionView(filteredWidgets)}
                </SectionComponent>
              </div>
            );
          }

          const cxCollapse = cx('ssp-player-section-collapse', {
            ['ssp-player-section-collapse__error']: sectionHasError,
            [VALIDATION_ERROR_MARKER_CLASS]: sectionHasError,
          });

          return (
            <Collapse
              bordered={false}
              key={index}
              defaultActiveKey={sectionExpanded ? index : undefined}
              collapsible={isCollapsible ? undefined : 'disabled'}
              expandIconPosition="right"
              className={cxCollapse}
            >
              <Collapse.Panel
                key={index}
                showArrow={isCollapsible}
                header={sectionName}
                style={{ border: 'none' }}
              >
                {this.renderSectionView(filteredWidgets)}
              </Collapse.Panel>
            </Collapse>
          );
        })}
      </Space>
    );
  };

  /**
   * Отображение экранных виджетов.
   */
  renderSectionView = (widgets: IWidget<IWidgetProps | IButtonRouteProps | IScreenMenuProps>[]) => {
    const { playerContext, interpreter, validationErrors, onlyShow, onClickNext, scriptName } =
      this.props;

    let gridMap: IWidget<null>[][] = [];

    widgets.forEach((widget: IWidget<null> | ICustomWidget<null>) => {
      if (!gridMap[widget.rowNumber]) {
        gridMap[widget.rowNumber] = [];
      }
      gridMap[widget.rowNumber].push(widget);
    });

    // Собираем ошибки в виджетах.
    let errorsInProps: IError[] = [];

    const VisibleWidgets = (
      <>
        {gridMap
          .filter(gridRow => isArray(gridRow))
          .map((screenWidgets: IWidget<null>[], rowIndex: number) => (
            <Row key={rowIndex} data-test-id="Screen.grid.row" gutter={[16, 16]}>
              {screenWidgets.map((widget: ICustomWidget<null>, i: number) => {
                const { WidgetComponent, props, additionProps } = this.getWidgetAndProps(widget);
                if (!WidgetComponent) {
                  return null;
                }

                // Общие свойства для всех компонент
                const commonProps = {
                  playerContext,
                  scriptName,
                  value: get(playerContext, widget.contextName),
                  onError: this.handleOnErrorWidget(rowIndex, i, widget.contextName),
                  interpreter,
                  isRequired: widget.isRequired,
                  onlyShow,
                  readOnly: screenWidgets[i]?.readOnly?.some(item =>
                    item?.expression
                      ? this.checkInterpreterExpression(item.expression, screenWidgets[i])
                      : false,
                  ),
                };

                const errors: IErrorBase[] = WidgetComponent.validator
                  ? WidgetComponent.validator({
                      ...props,
                      ...commonProps,
                    })
                  : [];

                if (errors.length > 0) {
                  errorsInProps.push(
                    ...errors.map((error: IError) => ({
                      ...error,
                      widgetInfo: { row: rowIndex, column: i },
                      additionalId: widget.contextName,
                    })),
                  );
                  return null;
                }

                // Для кнопок с переходами
                if (widget.component === EDefaultWidget.BUTTON_ROUTE) {
                  return (
                    <WidgetWrapper widget={widget} key={widget.index}>
                      <Col
                        data-test-id={`Screen.grid.widget.${widget.component}`}
                        xs={widget.width * 2}
                        offset={widget.offset * 2}
                        className={`${widget.component}_${widget.index}`}
                      >
                        <WidgetComponent
                          {...props}
                          {...commonProps}
                          onValidateClick={onClickNext}
                          onSwitchToBlock={this.props.onValidatedSwitchToBlock(widget)}
                        />
                      </Col>
                    </WidgetWrapper>
                  );
                  // Для меню с переходами
                } else if (widget.component === EDefaultWidget.MENU_ROUTE) {
                  return (
                    <WidgetWrapper widget={widget} key={widget.index}>
                      <Col
                        data-test-id={`Screen.grid.widget.${widget.component}`}
                        className={`${widget.component}_${widget.index}`}
                        xs={widget.width * 2}
                        offset={widget.offset * 2}
                      >
                        <WidgetComponent
                          {...props}
                          {...commonProps}
                          onValidateClick={onClickNext}
                          onSwitchToBlock={this.props.onSwitchToBlock}
                        />
                      </Col>
                    </WidgetWrapper>
                  );
                  // Для обычных виджетов
                } else {
                  let validationProps = {};
                  const errorForThisWidget = validationErrors.find(
                    error => error.source === widget,
                  );
                  if (errorForThisWidget) {
                    validationProps = {
                      hasError: true,
                      errorMessage: errorForThisWidget.message,
                    };
                  }
                  return (
                    <WidgetWrapper widget={widget} key={widget.index}>
                      <Col
                        data-test-id={`Screen.grid.widget.${widget.component}`}
                        className={`${widget.component}_${widget.index}`}
                        xs={widget.width * 2}
                        offset={widget.offset * 2}
                      >
                        {errorForThisWidget && <div className={VALIDATION_ERROR_MARKER_CLASS} />}
                        <WidgetComponent
                          {...props}
                          {...commonProps}
                          additionProps={additionProps}
                          onChange={onlyShow ? null : this.handleChangeWidget(widget)}
                          onBlur={this.handleBlurWidget(widget)}
                          {...validationProps}
                        />
                      </Col>
                    </WidgetWrapper>
                  );
                }
              })}
            </Row>
          ))}
      </>
    );

    if (errorsInProps.length > 0) {
      this.handleErrorWidget(errorsInProps);
    }

    return VisibleWidgets;
  };

  /**
   * Отображение панели с ошибками правил валидации от кнопок (Далее или  виджетов кнопок с переходами).
   */
  renderValidationFails = () => {
    const { validationErrors } = this.props;
    const nextErrors = validationErrors.filter(error => error.source === VALIDATION_SOURCE_NEXT);
    const buttonErrors = validationErrors.filter(
      error =>
        (error.source as IWidget<IButtonRouteProps>).component === EDefaultWidget.BUTTON_ROUTE,
    );

    if (isEmpty(nextErrors) && isEmpty(buttonErrors)) {
      return null;
    }

    return (
      <div data-test-id="Screen.ValidationFails" className={VALIDATION_ERROR_MARKER_CLASS}>
        <p>Нарушены заданные правила валидации</p>
        <p>
          {nextErrors.map((error, i) => (
            <span
              className="ant-form-item-explain-error"
              data-test-id="Screen.ValidationFails.nextError"
              key={i}
            >
              {error.message}
              <br />
            </span>
          ))}
          {buttonErrors.map((error, i) => (
            <span data-test-id="Screen.ValidationFails.buttonError" key={i}>
              {error.message}
              <br />
            </span>
          ))}
        </p>
      </div>
    );
  };

  render() {
    return (
      <div data-test-id="Screen.Component" className="screen ssp-player" ref={this.getScreenRef}>
        {this.renderValidationFails()}
        {this.renderGridView()}
        {this.renderDefaultButtons()}
      </div>
    );
  }
}
